const state = {
  currentCategoryId: null, // 当前选择的类别
  goodsCategoryValue: '', //显示的类别值
  searchValue: '',// 搜索值
  orderBy: 'category_id',// 按什么排序
  minPrice: 0,// 最低价格
  maxPrice: 999999999,// 最高价格
  currentPage: 1,// 当前页码
  pageSize: 2,// 每页的条目数
  itemCount: 0,// 不分页时查询到的条目数
  navValue: '',// 导航值
  userSelectedGoodsDetailCategoryValues: [// 目前允许一个商品最多有6个详细类别值
    {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
    {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
    {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
    {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
    {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
    {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
  ]// 用户选择的商品详细类别，第一次由后端查数据库生成
};

const getters = {
  /*height() {
    return state.userInfo.style.container.height;
  }*/
}

const mutations = {
  updateCurrentCategoryId(state, value) {
    state.currentCategoryId = value;
  },
  updateGoodsCategoryValue(state, value) {
    state.goodsCategoryValue = value;
  },
  updateSearchValue(state, value) {
    state.searchValue = value;
  },
  updateOrderBy(state, value) {
    state.orderBy = value;
  },
  updateMinPrice(state, value) {
    state.minPrice = value;
  },
  updateMaxPrice(state, value) {
    state.maxPrice = value;
  },
  updateCurrentPage(state, value) {
    state.currentPage = value;
  },
  updateItemCount(state, value) {
    state.itemCount = value;
  },
  updateNavValue(state, value) {
    state.navValue = value;
  },
  updateUserSelectedGoodsDetailCategoryValues(state, value) {
    state.userSelectedGoodsDetailCategoryValues = value;
  },
}

const actions = {
  updateCurrentCategoryId({commit}, value) {
    commit('updateCurrentCategoryId', value);
  },
  updateGoodsCategoryValue({commit}, value) {
    commit('updateGoodsCategoryValue', value);
  },
  updateSearchValue({commit}, value) {
    commit('updateSearchValue', value);
  },
  updateOrderBy({commit}, value) {
    commit('updateOrderBy', value);
  },
  updateMinPrice({commit}, value) {
    commit('updateMinPrice', value);
  },
  updateMaxPrice({commit}, value) {
    commit('updateMaxPrice', value);
  },
  updateCurrentPage({commit}, value) {
    commit('updateCurrentPage', value);
  },
  updateItemCount({commit}, value) {
    commit('updateItemCount', value);
  },
  updateNavValue({commit}, value) {
    commit('updateNavValue', value);
  },
  updateUserSelectedGoodsDetailCategoryValues({commit}, value) {
    commit('updateUserSelectedGoodsDetailCategoryValues', value);
  },
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



