<template>
  <div>
    <div :style="style.centerContainer">
      <!--显示图片-->
      <template v-if="imgUrl">
        <div>
          <img class="avatar" :style="style.avatar.img" :src="imgUrl" @click="chooseFile">
        </div>
      </template>
      <template v-else>
        <div class="avatar" :style="style.avatar.box" @click="chooseFile">
          <div :style="style.avatar.text">点击选择头像</div>
        </div>
      </template>
      <!--显示确认修改按钮-->
      <div>
        <el-button :style="style.avatar.uploadButton" type="success"
                   @click="fileUpload" icon="el-icon-upload">确认修改头像
        </el-button>
      </div>
    </div>

    <input type="file" style="display: none" ref="uploadFile" @change="onChoosedFile"/>  

  </div>
</template>

<
<script>
  export default {
    name: "avatarUpload",
    data() {
      return {
        style: {
          centerContainer: {
            width: this.scale(300) + 'px',
            display: 'flex',
            justifyContent: 'center',
            flexWrap: 'wrap'
          },
          avatar: {
            box: {
              borderRadius: '50%',
              width: this.scale(250) + 'px',
              height: this.scale(250) + 'px',
              lineHeight: this.scale(250) + 'px',
            },
            text: {
              margin: '0 auto',
              width: '6em',
              fontSize: this.scale(16) + 'px',
              color: '#52eb3d',
            },
            img: {
              width: this.scale(250) + 'px',
              height: this.scale(250) + 'px',// 200
              margin: this.scale(10) + 'px',// 200
              borderRadius: '50%',
            },
            uploadButton: {
              margin: this.scale(10) + 'px',
            }
          }
        },
        disabledUploadButton: true,
        imgUrl: this.$store.state.userInfo.user.userInfo.avatarSrc,
        errorMessage: '请先选择上传的文件！',
      };
    },

    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      chooseFile() {
        if (this.$refs.uploadFile.files != null) {// 选择文件前先清除前一次选中的文件
          this.$refs.uploadFile.files = null;
        }
        this.$refs.uploadFile.click()// 打开选择文件的窗口
      },
      /**
       * 当uploadFile标签中的files属性发生改变时调用
       * @returns {boolean} 当选择的文件是正确的大小和类型返回true;否则返回false
       */
      onChoosedFile() {
        let file = this.$refs.uploadFile.files[0];
        if (this.$refs.uploadFile.files != null && file != null) {
          if (file.size / 1024 / 1024 > 2)//大于2MB。
          {
            this.errorMessage = '头像文件不能超过2MB'
            this.$message({
              message: this.errorMessage,
              type: 'error',
              center: true,//文字居中
            });
            return false;
          } else if (file.type != 'image/jpeg' && file.type != 'image/png') {
            //console.log(file.type)
            this.errorMessage = '头像文件只能是jpg或png格式'
            this.$message({
              message: this.errorMessage,
              type: 'error',
              center: true,//文字居中
            });
            return false;
          } else {
            this.errorMessage = null;
            this.imgUrl = URL.createObjectURL((file));//可以访问这个imgUrl链接,也就是选择了图片后，显示选择的图片。当然任何文件都可以，不只是图片。
            return true;
          }
        }
      },
      fileUpload() {
        if (this.errorMessage != null) {
          this.$message({
            message: this.errorMessage,
            type: 'error',
            center: true,//文字居中
          });
          return;
        }
        let file = this.$refs.uploadFile.files[0];
        var formData = new FormData();
        formData.append("avatar", file);
        this.$page.put('/user/avatar', null,
          formData,
          response => {
            if (response.data != true) {
              this.$message({
                message: '上传失败',
                type: 'error',
                center: true,//文字居中
              });
            } else {
              this.$message({
                message: '上传成功',
                type: 'success',
                center: true,//文字居中
              });
            }
          },
          error => {
            this.$message({
              message: '上传失败',
              type: 'error',
              center: true,//文字居中
            });
          })
      },
    },
  }
</script>

<style>

  .avatar {
    cursor: pointer;
    border: 2px solid #fff;
  }

  .avatar:hover {
    cursor: pointer;
    border: 2px dashed cyan;
  }


</style>



