//对访问页面时的验权的请求进行封装，
// 这样即使换了其他api，也只需要改这里即可。
//请求格式：/authorization/页面名/具体请求

var root = 'http://www.ryan1h.top/authorization'
//var root = 'http://localhost:81/authorization'
// 引用axios
var axios = require('axios')

function authorizationAxios(method, url, params, data, success, failure) {
  axios({
    method: method,
    url: url,
    params: params,//url中的参数，如果没提供就使用null
    data: data,//body中的参数，只适用于PUT,POST,PATCH，如果没提供就使用null
    baseURL: root,
    withCredentials: false//表示跨域请求时是否需要使用凭证
  })
    .then(function (response) {
      if (success) {//如果提供了一个success参数(一个函数)
        success(response)//当获取到响应时就调用提供的success(),response=>{console.log(response.data)}
      }
    })
    .catch(function (error) {
      if (failure) {//如果提供了一个failure参数(一个函数)
        failure(error.response)//当发生错误时就调用提供的failure(),error=>{console.log(error.data)}
      }
    })
}

//使用的时候，如果没有params，那么就填null
export default {
  get: function (url, params, data, success, failure) {
    return authorizationAxios('GET', url, params, data, success, failure)
  },
  post: function (url, params, data, success, failure) {
    return authorizationAxios('POST', url, params, data, success, failure)
  },
  put: function (url, params, data, success, failure) {
    return authorizationAxios('PUT', url, params, data, success, failure)
  },
  delete: function (url, params, data, success, failure) {
    return authorizationAxios('DELETE', url, params, data, success, failure)
  }
}



