<template>
  <div :style="style.container">

    <!--header-->
    <hx-home-header :show-carousel="true"></hx-home-header>

    <!--侧边导航-->
    <hx-sideNav></hx-sideNav>

    <!--商品展示-->
    <div :style="style.goodsContainer">
      <hx-home-goods/>
    </div>

    <!--底栏-->
    <hx-home-footer></hx-home-footer>

  </div>
</template>

<script>
  import pullDownNav from './pullDownNav'
  import sideNav from './sideNav'
  import homeGoods from '../../components/goods/homeGoods'
  import homeHeader from './homeHeader'
  import homeFooter from './homeFooter'

  export default {
    components: {
      'hx-pullDownNav': pullDownNav,
      'hx-sideNav': sideNav,
      'hx-home-goods': homeGoods,
      'hx-home-header': homeHeader,
      'hx-home-footer': homeFooter,
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
    },
    data() {
      return {
        style: {
          container: {
            //width: this.scale(1366) - window.innerWidth + document.body.clientWidth + 'px',
          },
          goodsContainer: {
            backgroundColor: "#f0f0f0",
            padding: this.scale(50) + 'px 0',
          },

        },//style end
      }
    },
  }
</script>

<style>

</style>



