<!--
使用举例:
    只需传入一个需要放大的图片的src即可
    <hx-magnifier :originalImgSrc="originalImgSrc" ></hx-magnifier>
-->
<template>
  <div :style="style.container">
    <!--放大图片-->
    <div :style="style.magnify.box">
      <img id="magnifyImg" :style="style.magnify.img" :src="originalImgSrc"/>
    </div>
    <!--缩小图片-->
    <div :style="style.shrink.box"
         @mousemove="shrinkImgMove"
         @mouseleave="shrinkImgLeave">
      <div id="magnifier" :style="style.shrink.magnifier">
      </div>
      <img id="shrinkImg" :src="originalImgSrc" :style="style.shrink.img"/>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'hx-magnifier',
    computed: {
      shrinkImg() {// 缩小的图片
        return document.querySelector("#shrinkImg");
      },
      shrinkImgWidth() {// 缩小的图片的宽度
        return this.shrinkImg.style.width.substr(0, this.shrinkImg.style.width.length - 2);
      },
      shrinkImgHeight() {// 缩小的图片的高度
        return this.shrinkImg.style.height.substr(0, this.shrinkImg.style.height.length - 2);
      },
      magnifier() {// 放大镜
        return document.querySelector("#magnifier");
      },
      magnifyImg() {// 放大的图片
        return document.querySelector("#magnifyImg");
      }
    },
    data() {
      return {
        style: {
          container: {
            position: 'relative',
          },
          magnify: {
            box: {
              position: 'absolute',
              width: this.scale(400) + 'px',
              height: this.scale(400) + 'px',
              left: this.scale(430) + 'px',
              zIndex: 999,
              overflow: 'hidden',
              border: '1px solid #eee',
              display: 'none',
            },
            borderBox: {
              padding: this.scale(10) + 'px',
              backgroundColor: 'white',
              border: '1px solid #eee',
            },
            img: {
              position: 'absolute'
            }
          },
          shrink: {
            box: {
              position: 'relative',
              display: 'inline-block',
              width: this.scale(400) + 'px',
              height: this.scale(400) + 'px',
            },
            magnifier: {
              visibility: 'hidden',
              width: this.scale(200) + 'px',
              height: this.scale(200) + 'px',
            },
            img: {
              width: this.scale(400) + 'px',
              height: this.scale(400) + 'px',
            }
          },
        },// style end
      }
    },
    props: {
      originalImgSrc: {// 要求尽量是1:1的图片，最好是800*800
        type: String,
      },
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      shrinkImgMove(event) {
        this.magnifier.style.visibility = 'visible';
        this.style.magnify.box.display = 'block';
        let effectiveMovement = 0;
        if ((event.clientX - this.shrinkImg.getBoundingClientRect().left) <= this.shrinkImgWidth / 4) {
          this.magnifyImg.style.left = '0px';
          this.magnifier.style.left = '0px';
        } else if ((event.clientX - this.shrinkImg.getBoundingClientRect().left) > this.shrinkImgWidth / 4 && (event.clientX - this.shrinkImg.getBoundingClientRect().left) < this.shrinkImgWidth * 3 / 4) {
          effectiveMovement = event.clientX - this.shrinkImg.getBoundingClientRect().left - this.shrinkImgWidth / 4;
          this.magnifyImg.style.left = effectiveMovement * -2 + 'px';
          this.magnifier.style.left = effectiveMovement + 'px';
        } else {
          this.magnifyImg.style.left = -this.shrinkImgWidth + 'px';
          this.magnifier.style.left = this.shrinkImgWidth / 2 + 'px';
        }

        if ((event.clientY - this.shrinkImg.getBoundingClientRect().top) <= this.shrinkImgHeight / 4) {
          this.magnifyImg.style.top = '0px';
          this.magnifier.style.top = '0px';
        } else if ((event.clientY - this.shrinkImg.getBoundingClientRect().top) > this.shrinkImgHeight / 4 && (event.clientY - this.shrinkImg.getBoundingClientRect().top) < this.shrinkImgHeight * 3 / 4) {
          effectiveMovement = event.clientY - this.shrinkImg.getBoundingClientRect().top - this.shrinkImgHeight / 4;
          this.magnifyImg.style.top = effectiveMovement * -2 + 'px';
          this.magnifier.style.top = effectiveMovement + 'px';
        } else {
          this.magnifyImg.style.top = -this.shrinkImgHeight + 'px';
          this.magnifier.style.top = this.shrinkImgHeight / 2 + 'px';
        }
      },

      shrinkImgLeave() {
        this.magnifier.style.visibility = 'hidden';
        this.style.magnify.box.display = 'none';
      }
    },

  }
</script>

<style scoped>
  #magnifier {
    background-image: url(../../assets/test/point.png);
    cursor: move;
    position: absolute;
    z-index: 999;
    background-repeat: repeat
  }
</style>



