<template>
  <div id="app" :style="setAppStyle()">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        visibleWidth: window.screen.width * window.devicePixelRatio,// 显示区域的宽度:屏幕物理宽度 - 滚动条宽度
      }
    },
    methods: {
      /**
       * 获取竖向滚动条宽度
       * @returns {number}
       */
      getScrollWidth() {
        let noScroll, scroll, oDiv = document.createElement("DIV");
        oDiv.style.cssText = "position:absolute; top:-1000px; width:100px; height:100px; overflow:hidden;";
        noScroll = document.body.appendChild(oDiv).clientWidth;
        oDiv.style.overflowY = "scroll";
        scroll = oDiv.clientWidth;
        document.body.removeChild(oDiv);
        return noScroll - scroll;
      },
      setAppStyle() {
        let style = {
          margin: '0 auto',
          width: this.visibleWidth + 'px'
        }
        return style;
      },

      setVisibleWidth() {
        if (document.documentElement.scrollHeight > window.innerHeight) {// 出现垂直方向的滚动条时
          this.visibleWidth = window.screen.width * window.devicePixelRatio - this.getScrollWidth();
        } else {
          this.visibleWidth = window.screen.width * window.devicePixelRatio;
        }
        this.$store.dispatch('app/updateVisibleWidth', this.visibleWidth)
      }
    },
    mounted() {
      this.setVisibleWidth();
    },
    updated() {
      this.setVisibleWidth();
    }
  }
</script>

<style>

</style>



