import Vue from 'vue'
import Vuex from 'vuex'
import userInfo from './modules/userInfo.js'
import app from './modules/app.js'
import goodsShow from './modules/goodsShow.js'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    userInfo,
    app,
    goodsShow
  }
})



