//不要使用@来构造路径，虽然它代表src目录。
// 因为这样webstorm无法识别，因此移动一个文件到另一个目录时，需要手动修改目录路径。
//而使用相对路径就会被webstorm识别，就不需要手动修改目录路径。
import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home/home'
import forgetPassword from '../components/login/forgetPassword'
import login from '../components/login/login'
import page401 from '../components/errorPage/page401'
import page404 from '../components/errorPage/page404'
import page500 from '../components/errorPage/page500'
import test from '../components/test/test'
import userInfo from '../components/user/userInfo'
import shopcart from '../components/user/shopcart'
import collect from '../components/user/collect'
import allOrder from '../components/user/allOrder'
import editPersonalInfo from '../components/user/editPersonalInfo'
import goodsDetail from '../components/goods/goodsDetail'
import goodsDetailCategory from '../components/goods/goodsDetailCategory'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    return {x: 0, y: 0}// 切换新路由后滚动到页面顶部
  },
  routes: [
    {
      path: '/',
      component: home,
      name: 'home',
      meta: {
        title: '欢迎访问王姐超市网站'
      },
    },
    {
      path: '/login',
      component: login,
      name: 'login',
      props: true,
      meta: {
        title: '登录'
      }
    },
    {
      path: '/forgetPassword',
      component: forgetPassword,
      name: 'forgetPassword',
      meta: {
        title: '找回密码'
      },
    },
    {
      path: '/userInfo',
      name: 'userInfo',
      component: userInfo,
      meta: {
        title: '个人中心'
      },
      children: [{
        path: 'shopcart',
        component: shopcart,
        name: 'userInfoShopcart',
        meta: {
          title: '购物车'
        },
      },
        {
          path: 'collect',
          component: collect,
          name: 'userInfoCollect',
          meta: {
            title: '我的收藏夹'
          },
        },
        {
          path: 'allOrder',
          component: allOrder,
          name: 'userInfoAllOrder',
          meta: {
            title: '所有订单'
          },
        },]
    },
    {
      path: '/goodsDetail/:goodsParentId',
      name: 'goodsDetail',
      props: true,
      component: goodsDetail,
      meta: {
        title: '王姐超市'
      },
    },
    {
      path: '/goodsDetailCategory',
      name: 'goodsDetailCategory',
      component: goodsDetailCategory,
      meta: {
        title: '王姐超市'
      },
    },
    {
      path: '/editPersonalInfo',
      name: 'editPersonalInfo',
      component: editPersonalInfo,
      meta: {
        title: '修改个人信息'
      },
    },
    {
      path: '/test',
      component: test,
      meta: {
        title: 'test'
      },
    },
    {
      path: '/401',
      component: page401,
      meta: {
        title: '401 Unauthorized'
      },
    },
    {
      path: '/404',
      component: page404,
      meta: {
        title: '404 Not Found'
      },
    },
    {
      path: '/500',
      component: page500,
      meta: {
        title: 'Internal Server Error'
      },
    },
  ],
})



