<template>

  <!--todo: 有时间再做侧边导航-->
  <!--<ul :style="style.sideNavUl">
    <li>
      <span>口碑爆款</span>
    </li>
    <li>
      <span>大牌满减</span>
    </li>
    <li>
      <span>美容彩妆</span>
    </li>
    <li>
      <span>母婴儿童</span>
    </li>
    <li>
      <span>家居护理</span>
    </li>
  </ul>-->

</template>

<script>
  export default {
    name: "sideNav",
    data() {
      return {
        style: {
          sideNavUl: {}
        }
      }
    },
    methods: {

      scale(value) {
        return this.$root.scale(value);
      },

      // 鼠标是否在导航菜单上的处理
      isSideNavMenuOnHover(isHover) {
        if (isHover == true) {// 鼠标进入'所有分类'
          this.style.sideNavOnHover = true;
          this.style.sideNavPullDownBox.visibility = 'visible';//使导航菜单项可见
        } else {// 鼠标离开'所有分类'
          setTimeout(function () {
            if (this.style.sideNavPullDownOnHover == false) {// 延迟一ms再判断是否也离开了导航菜单选项
              this.style.sideNavPullDownBox.visibility = 'hidden'
              this.style.sideNavOnHover = false;
            }
          }.bind(this), 1)
        }
      },

      // 鼠标是否在导航菜单的选项上的处理
      isSideNavMenuOptionOnHover(isHover) {
        if (isHover == true) {// 鼠标进入导航菜单选项
          this.style.sideNavPullDownOnHover = true;
          if (this.style.sideNavPullDownBox.visibility == 'visible') { // 如果此时导航菜单也可见
            this.style.sideNavDetail.visibility = 'visible';// 那么菜单项详情也应该可见
          }
        } else {// 鼠标离开导航菜单选项
          setTimeout(function () {
            if (this.style.sideNavPullDownDetailOnHover == false) {// 延迟一ms再判断是否也离开了菜单项详情
              this.style.sideNavPullDownBox.visibility = 'hidden'
              this.style.sideNavPullDownOnHover = false;
              this.style.sideNavOnHover = false;
              this.style.sideNavDetail.visibility = 'hidden';
            } else {
              this.style.sideNavDetail.visibility = 'visible';
            }
          }.bind(this), 1)
        }
      },

      // 鼠标是否在导航菜单项的详情上的处理
      isSideNavMenuOptionDetailOnHover(isHover) {
        if (isHover == true) {
          this.style.sideNavPullDownDetailOnHover = true;
        } else {
          this.style.sideNavPullDownDetailOnHover = false;
          this.style.sideNavDetail.visibility = 'hidden';
          this.style.sideNavPullDownOnHover = false;
          setTimeout(function () {
            if (this.style.sideNavPullDownOnHover == false) {// 延迟1ms再判断鼠标是否没有进入导航菜单选项
              this.style.sideNavPullDownBox.visibility = 'hidden';
            }
          }.bind(this), 1)
        }
      },

      // 鼠标在哪一个导航菜单项上，就设置菜单项详情的数据
      setSideNavDetail(index) {
        switch (index) {
          case 0:
            this.sideNavDetetail = this.sideNavDetetailBeauty;
            break;
          case 1:
            this.sideNavDetetail = this.sideNavDetetailBaby;
            break;
          case 2:
            this.sideNavDetetail = this.sideNavDetetailBeauty;
            break;
          case 3:
            this.sideNavDetetail = this.sideNavDetetailBeauty;
            break;
          case 4:
            this.sideNavDetetail = this.sideNavDetetailBaby;
            break;
        }
      }
    },
    // methods end
  }
</script>

<style scoped>
  ul {
    list-style-type: none;
  }

  dl, dd, dt, li, ul {
    padding: 0px;
    margin: 0px;
  }


</style>



