<template>
  <div>
    <!-- top -->
    <ul :style="style.top.box">
      <li :style="style.top.left">
        <span :style="style.top.homeFont" @click="homeClick">王姐超市欢迎您!</span>
      </li>
      <template v-if="userLoaded">
        <li :style="style.top.right">
          <span :style="style.top.logOut" @click="logoutClick">退出</span>
        </li>
        <li :style="style.top.right">
          <el-tooltip effect="dark" :content="tooltipContent" placement="bottom">
            <img @click="avatarClick" :style="style.top.avatar"
                 :src="this.$store.state.userInfo.user.userInfo.avatarSrc">
          </el-tooltip>
        </li>
      </template>
      <template v-else>
        <li :style="style.top.loginRegiste">
          <span :style="style.top.topFont" @click="loginClick">登录</span>
        </li>
        <li :style="style.top.loginRegiste">
          <span :style="style.top.topFont" @click="registeClick">注册</span>
        </li>
      </template>
      <li :style="style.top.right">
        <span :style="style.top.topFont" @click="orderClick">我的订单</span>
      </li>
    </ul>
    <!--logo, 搜索框, 购物车-->
    <div :style="style.logoTop.box">
      <!--logo图片-->
      <a href="javascript:;" @click="homeClick">
        <img :style="style.logoTop.logoImg" src="../../assets/home/header/logo.png"/>
      </a>
      <!--搜索按钮-->
      <span :style="style.logoTop.searchBtn">
        <el-input placeholder="" v-model="searchValue">
          <el-button slot="append" icon="el-icon-search" @click="searchGoodsParentBtnClick"></el-button>
        </el-input>
      </span>
      <!--购物车按钮-->
      <span :style="style.logoTop.shoppingBtn.box" @click="shopcartClick">
        <img src="../../assets/home/header/shop-cart.png" :style="style.logoTop.shoppingBtn.shoppingIcon"/>
        <span :style="style.logoTop.shoppingBtn.shoppingText">
          购物车
        </span>
      </span>
    </div>
    <!--下拉导航栏和轮播-->
    <hx-pull-down-nav :show-carousel="showCarousel"></hx-pull-down-nav>
  </div>
</template>

<script>
  import pullDownNav from "./pullDownNav";

  export default {
    name: "hx-home-header",
    components: {
      'hx-pull-down-nav': pullDownNav
    },
    props: {
      // 是否显示轮播组件
      showCarousel: {
        type: Boolean,
        default: false,
      }
    },

    computed: {
      searchValue: {
        get() {
          return this.$store.state.goodsShow.searchValue
        },
        set(value) {
          this.$store.commit('goodsShow/updateSearchValue', value)
        }
      }
    },

    data() {
      return {
        style: {
          top: {
            box: {
              backgroundColor: '#57555e',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
            },
            left: {
              marginLeft: this.scale(50) + 'px',
              float: 'left',
            },
            homeFont: {
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer',
            },
            topFont: {
              color: '#bacede',
              fontSize: this.scale(15) + 'px',
              cursor: 'pointer',
            },
            loginRegiste: {
              marginLeft: this.scale(50) + 'px',
              float: 'left',
            },
            avatar: {
              borderRadius: '50%',
              width: this.scale(45) + 'px',
              height: this.scale(45) + 'px',
              verticalAlign: "middle",
              cursor: 'pointer'
            },
            logOut: {
              marginLeft: this.scale(50) + 'px',
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer'
            },
            right: {
              float: 'right',
              marginRight: this.scale(70) + 'px',
            },
          },
          logoTop: {
            box: {
              marginTop: this.scale(20) + 'px',
              height: this.scale(80) + 'px',
            },
            logoImg: {
              marginLeft: this.scale(70) + 'px',
              width: this.scale(167) + 'px',
              height: this.scale(70) + 'px',
              verticalAlign: 'bottom',
            },
            searchBtn: {
              display: 'inline-block',
              marginLeft: this.scale(225) + 'px',
              width: this.scale(400) + 'px',
              verticalAlign: 'bottom',
            },
            shoppingBtn: {
              box: {
                display: 'inline-block',
                marginLeft: this.scale(100) + 'px',
                padding: this.scale(2) + 'px',
                width: this.scale(110) + 'px',
                height: this.scale(36) + 'px',
                lineHeight: this.scale(36) + 'px',
                textAlign: 'center',
                border: this.scale(2) + 'px solid #f7261d',
                borderRadius: this.scale(20) + 'px',
                cursor: 'pointer',
                verticalAlign: 'bottom',
              },
              shoppingIcon: {
                marginLeft: this.scale(10) + 'px',
                verticalAlign: 'middle',
              },
              shoppingText: {
                marginLeft: this.scale(5) + 'px',
                fontSize: this.scale(15) + 'px',
                verticalAlign: 'middle',
              }
            },
          },
        },// style end
        tooltipContent: '',
        userLoaded: false,
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      homeClick() {
        this.$router.push({name: 'home'})
      },
      loginClick() {
        this.$router.push({name: "login", params: {tabName: 'login'}})
      },
      registeClick() {
        this.$router.push({name: "login", params: {tabName: 'registe'}})
      },
      logoutClick() {
        this.$page.post('/logout')
        this.$message({
          message: '您已退出登录!',
          type: 'success',
          center: true,//文字居中
        });
        // 重置身份信息
        this.$store.dispatch('userInfo/updateUser',
          {
            id: null,
            username: null,
            userInfo: {
              nickname: null,
              sex: null,
              birthday: 0,
              hometown: "['510000', '510100', '510116']",
              deliverAddress: "['510000', '510100', '510116']",// 收货地址
              addressDetail: null,// 收货详细地址
            }
          } // 保存登录后的用户信息
        )
        setTimeout(() => {
          window.location.reload()
        }, 300)
      },

      setTooltipContent(username) {
        let h = new Date().getHours();
        if (h > 0 && h <= 12) {
          this.tooltipContent = '早上好! ' + username;
        } else if (h > 12 && h < 18) {
          this.tooltipContent = '下午好! ' + username;
        } else {
          this.tooltipContent = '晚上好! ' + username;
        }
      },
      avatarClick() {
        this.$router.push({name: 'editPersonalInfo'})
      },
      orderClick() {
        this.$router.push({name: 'userInfoAllOrder'})
      },
      shopcartClick() {
        this.$router.push({name: 'userInfoShopcart'})
      },
      /**
       * 只能按搜索值或者按类别搜索，不能既按搜索值又联合类别进行搜索
       */
      searchGoodsParentBtnClick() {
        if (this.searchValue === "")
          return;
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues",
          [
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
          ]
        );
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        this.$store.commit("goodsShow/updateMinPrice", 0);// 初始化查询条件
        this.$store.commit("goodsShow/updateMaxPrice", 999999999);// 初始化查询条件
        this.$store.commit("goodsShow/updateCurrentCategoryId", null);// 点击搜索时，需要清除currentCategoryId
        document.location.hash = "/";// 先随便修改地址栏为一个值，这样在同一个路由上只变化query地址栏也会更新。
        let query = {searchValue: this.searchValue};
        this.$router.push({name: 'goodsDetailCategory', query: query})
      }
    },

    created() {
      this.$authorization.get('/login', null, null, response => {
        if (response.status == 200) {
          this.$store.dispatch('userInfo/updateUser', response.data)
          this.setTooltipContent(this.$store.state.userInfo.user.username);
          this.userLoaded = true;
        }
      })
    }
  }
</script>

<style scoped>
  span, div, dl, dd, dt, li, ul {
    padding: 0;
    margin: 0;
    border: 0;
    list-style-type: none;
  }

  @font-face {
    font-family: 'webfont';
    src: url('//at.alicdn.com/t/webfont_9096m9plwtm.eot'); /* IE9*/
    src: url('//at.alicdn.com/t/webfont_9096m9plwtm.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */ url('//at.alicdn.com/t/webfont_9096m9plwtm.woff') format('woff'), /* chrome、firefox */ url('//at.alicdn.com/t/webfont_9096m9plwtm.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/ url('//at.alicdn.com/t/webfont_9096m9plwtm.svg#思源黑体-粗') format('svg'); /* iOS 4.1- */
  }
</style>



