<template>
  <div>
    <!-- header -->
    <div :style="style.header.box">
      <div :style="[style.header.home]" @click="homeClick">
        王姐超市欢迎您!
      </div>
      <div :style="style.header.right">
        <span :style="style.header.logOut" @click="logoutClick">退出</span>
      </div>
    </div>

    <div :style="style.backgroundBox">
      <div :style="style.centerContainer">
        <div :style="style.avatar">
          <!--头像-->
          <hx-avatar-upload></hx-avatar-upload>
        </div>

        <div :style="style.personalInfo.box">
          <!--账号-->
          <div :style="style.personalInfo.id">{{username}}</div>

          <!--用户可输入的信息-->
          <div :style="style.personalInfo.form.box">
            <el-form>
              <!--昵称-->
              <div :style="style.personalInfo.form.itemBox">
                <span :style="style.personalInfo.form.labelText">昵称:</span>
                <span :style="style.personalInfo.form.nickname">
                <el-input type="text" v-model="personalInfoForm.nickname" autocomplete="on"
                          maxLength="24">
                </el-input>
              </span>
              </div>
              <!--性别-->
              <div :style="style.personalInfo.form.itemBox">
                <span :style="style.personalInfo.form.labelText">性别:</span>
                <span :style="style.personalInfo.form.sex">
                  <el-switch
                    v-model="personalInfoForm.sex"
                    active-color="#69db29"
                    inactive-color="#3bf1ec"
                    active-text="女"
                    inactive-text="男">
                  </el-switch>
                </span>
              </div>
              <!--生日-->
              <div :style="style.personalInfo.form.itemBox">
                <span :style="style.personalInfo.form.labelText">生日:</span>
                <span :style="style.personalInfo.form.birthday">
                   <el-date-picker
                     v-model="personalInfoForm.birthday"
                     align="left"
                     placeholder="点击选择日期"
                     type="date"
                     :picker-options="dateOptions">
                    </el-date-picker>
                </span>
              </div>
              <!--家乡-->
              <div :style="style.personalInfo.form.itemBox">
                <span :style="style.personalInfo.form.labelText">家乡:</span>
                <span :style="style.personalInfo.form.hometown">
                   <el-cascader
                     filterable
                     separator="-"
                     expand-trigger="hover"
                     :options="areaOptions"
                     v-model="personalInfoForm.hometown">
                  </el-cascader>
                </span>
              </div>
              <!--收货地址-->
              <div :style="style.personalInfo.form.itemBox">
                <span :style="style.personalInfo.form.labelText">收货地址:</span>
                <span :style="style.personalInfo.form.hometown">
                   <el-cascader
                     filterable
                     separator="-"
                     expand-trigger="hover"
                     :options="areaOptions"
                     v-model="personalInfoForm.deliverAddress">
                  </el-cascader>
                </span>
              </div>
              <!--详细地址-->
              <div :style="style.personalInfo.form.itemBox">
                <span :style="style.personalInfo.form.labelText">详细地址:</span>
                <span :style="style.personalInfo.form.birthday">
                   <el-input
                     type="textarea"
                     :autosize="{maxRows: 3}"
                     placeholder="请输入街道、小区、楼栋号、单元号、门牌号"
                     maxLength="50"
                     v-model="personalInfoForm.addressDetail">
                  </el-input>
                </span>
              </div>
              <!--详细地址-->
              <div :style="style.personalInfo.form.itemBox">
                <div :style="style.personalInfo.form.submitBtn">
                  <el-button @click="onSubmit">修改</el-button>
                </div>
              </div>
            </el-form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import avatarUpload from './avatarUpload'
  import areaJson from '../../json/area'

  export default {
    name: "editPersonalInfo",
    components: {
      'hx-avatar-upload': avatarUpload,
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      homeClick() {
        this.$router.push({name: "home"})
      },
      logoutClick() {
        this.$page.post('/logout');
        this.$message({
          message: '您已退出登录!',
          type: 'success',
          center: true,//文字居中
        });
        // 重置身份信息
        this.$store.dispatch('userInfo/updateUser',
          {
            id: null,
            username: null,
            userInfo: {
              nickname: null,
              sex: null,
              birthday: 0,
              hometown: "['510000', '510100', '510116']",
              deliverAddress: "['510000', '510100', '510116']",// 收货地址
              addressDetail: null,// 收货详细地址
            }
          } // 保存登录后的用户信息
        )
        setTimeout(() => {
          window.location.reload()
        }, 300)
      },
      onSubmit() {
        let userInfo = JSON.parse(JSON.stringify(this.personalInfoForm))
        userInfo.hometown = JSON.stringify(userInfo.hometown);
        userInfo.deliverAddress = JSON.stringify(userInfo.deliverAddress)
        userInfo.userId = this.$store.state.userInfo.user.id;
        this.$page.put("/user/userInfo", null, userInfo,
          res => {
            if (res.data == true) {
              this.$message({
                message: '修改成功!',
                type: 'success',
                center: true,//文字居中
              });
            }
          },
          error => {
            this.$message({
              message: '修改失败!,请稍后重试',
              type: 'error',
              center: true,//文字居中
            });
          }
        )
      },

    },
    data() {
      return {
        style: {
          header: {
            box: {
              backgroundColor: '#57555e',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
            },
            home: {
              marginLeft: this.scale(50) + 'px',
              float: 'left',
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer'
            },
            logOut: {
              marginLeft: this.scale(50) + 'px',
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer'
            },
            right: {
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
              float: 'right',
              marginRight: this.scale(70) + 'px',
            },
            user: {
              fontSize: this.scale(18) + 'px',
              color: 'cyan',
            }
          },
          backgroundBox: {
            background: 'linear-gradient(to right,#215422,#2e3d37,#0c1d17)',
            minHeight: this.scale(window.innerHeight - 50) + 'px',// 视口高 - 顶栏
          },
          centerContainer: {
            width: this.scale(800) + 'px',
            margin: '0 auto',
          },
          avatar: {
            display: 'inline-block',
            verticalAlign: 'middle',
          },
          personalInfo: {
            box: {
              display: 'inline-block',
              marginLeft: this.scale(100) + 'px',
              verticalAlign: 'middle',
            },
            id: {
              fontSize: this.scale(40) + 'px',
              fontWeight: this.scale(500),
              marginBottom: this.scale(10) + 'px',
            },
            form: {
              box: {
                fontWeight: 'bold',
              },
              itemBox: {
                margin: this.scale(20) + "px " + this.scale(0) + 'px',
              },
              labelText: {
                color: '#777a7e',
                verticalAlign: "middle",
                display: 'inline-block',
                width: this.scale(100) + 'px',
              },
              nickname: {
                display: 'inline-block',
                width: this.scale(250) + 'px',
                verticalAlign: "middle",
              },
              sex: {
                display: 'inline-block',
                width: this.scale(150) + 'px',
                verticalAlign: "middle",
              },
              birthday: {
                display: 'inline-block',
                width: this.scale(250) + 'px',
                verticalAlign: "middle",
              },
              hometown: {
                display: 'inline-block',
                width: this.scale(250) + 'px',
                verticalAlign: "middle",
              },
              submitBtn: {
                margin: '0 auto',
                width: this.scale(70) + 'px',
              }
            }
          },
        },// style end
        username: this.$store.state.userInfo.user.username,
        personalInfoForm: {
          nickname: this.$store.state.userInfo.user.userInfo.nickname,
          sex: this.$store.state.userInfo.user.userInfo.sex == '男' ? false : true,// true代表女性,false代表男性
          birthday: new Date(this.$store.state.userInfo.user.userInfo.birthday),
          hometown: eval('(' + this.$store.state.userInfo.user.userInfo.hometown + ')'),
          deliverAddress: eval('(' + this.$store.state.userInfo.user.userInfo.deliverAddress + ')'),// 收货地址
          addressDetail: this.$store.state.userInfo.user.userInfo.addressDetail,// 收货详细地址
        },
        // 性别候选项
        sexOptions: [{
          value: '男',
          label: '男'
        }, {
          value: '女',
          label: '女'
        }],
        // 日期候选项
        dateOptions: {
          disabledDate(time) {//配置禁用的日期
            return time.getTime() > Date.now(); //大于当前时间的那些日期被禁用
          },
        },
        // 地区候选项
        areaOptions: areaJson,
        baseContextPath: this.$route.path,
      }
    },
    beforeCreate() {
      this.$root.handleLoginAuthorization("/login");
    },
  }
</script>

<style scoped>

  #kkkkkk {
    color: cyan;
  }

</style>



