<template>
  <div :style="style.container">
    <!-- header -->
    <div :style="style.header.box">
      <div :style="[style.header.home]" @click="homeClick">
        王姐超市欢迎您!
      </div>
      <div :style="style.header.right">
        <span :style="style.header.logOut" @click="logoutClick">退出</span>
      </div>
      <div :style="style.header.right">
        <el-tooltip effect="dark" :content="tooltipContent" placement="bottom">
          <img @click="avatarClick" :style="style.header.avatar"
               :src="this.$store.state.userInfo.user.userInfo.avatarSrc">
        </el-tooltip>
      </div>
    </div>
    <!-- sidebar -->
    <div :style="style.sidebar.box">
      <div id="sidebar-allFunction" :style="style.sidebar.allFunction">
        <span>所有功能</span>
      </div>
      <!-- sidebar content -->
      <div id="sidebar-content" :style="style.sidebar.content.box">
        <router-view></router-view>
      </div>
      <ul :style="style.sidebar.ul">
        <!-- 购物车 -->
        <li class="sideBarLi" :style="[style.sidebar.li,style.sidebar.topLi]">
          <div :style="style.sidebar.clickStyle[0]" @click="tabClick(0,'/userInfo/shopcart')">
            购物车
          </div>
        </li>
        <!-- 收藏夹 -->
        <li :style="style.sidebar.li" @click="tabClick(1,'/userInfo/collect')">
          <div :style="style.sidebar.clickStyle[1]">
            收藏夹
          </div>
        </li>
        <!-- 所有订单 -->
        <li :style="style.sidebar.li" @click="tabClick(2,'/userInfo/allOrder')">
          <div :style="style.sidebar.clickStyle[2]">
            所有订单
          </div>
        </li>
      </ul>
    </div>
  </div>

</template>

<script>
  import avatarUpload from './avatarUpload'

  export default {
    name: "userInfo",
    components: {
      'hx-avatarUpload': avatarUpload
    },
    data() {
      return {
        style: {
          container: {
            /*background: 'linear-gradient(to bottom, #eef0f1, #e2e3e4)',
            height: '0px',
            backgroundSize: 0 + 'px ' + 0 + 'px',*/
          },
          header: {
            box: {
              backgroundColor: '#57555e',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
            },
            home: {
              marginLeft: this.scale(50) + 'px',
              float: 'left',
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer'
            },
            logOut: {
              marginLeft: this.scale(50) + 'px',
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer'
            },
            right: {
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
              float: 'right',
              marginRight: this.scale(70) + 'px',
            },
            avatar: {
              borderRadius: '50%',
              width: this.scale(45) + 'px',
              height: this.scale(45) + 'px',
              verticalAlign: "middle",
              cursor: 'pointer'
            }
          },
          sidebar: {
            box: {
              margin: this.scale(40) + 'px',
              position: 'relative',
            },
            allFunction: {
              height: this.scale(35) + 'px',
              lineHeight: this.scale(35) + 'px',
              fontSize: this.scale(22) + 'px',
              color: '#5988b1',
              fontWeight: 'bold'
            },
            content: {
              box: {
                position: 'absolute',
                left: this.scale(200) + 'px',
                width: this.scale(1050) + 'px',
              }
            },
            ul: {
              width: this.scale(120) + 'px',
              marginLeft: this.scale(20) + 'px',
              marginTop: this.scale(60) + 'px',
              padding: this.scale(5) + 'px',
            },
            li: {
              cursor: 'pointer',
              borderLeft: this.scale(3) + 'px inset #c1e7cf',
              borderRight: this.scale(1) + 'px solid #82d39e',
              borderBottom: this.scale(2) + 'px groove #c1e7cf',
              borderRadius: this.scale(8) + 'px',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
              textAlign: 'center',
              color: '#494b4a',
            },
            topLi: {
              borderTop: this.scale(3) + 'px inset #c1e7cf',
            },
            clickStyle: [
              // 购物车被点击时的样式
              {
                background: null,
                color: null,
                fontWeight: null,
              },// 收藏被点击时的样式
              {
                background: null,
                color: null,
                fontWeight: null,
              },// 所有订单被点击时的样式
              {
                background: null,
                color: null,
                fontWeight: null,
              },
            ],
          }
        },// style end
        tooltipContent: '',
      };
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      getScrollWidth() {
        return this.$root.getScrollWidth();
      },
      homeClick() {
        this.$router.push({name: "home"})
      },
      logoutClick() {
        this.$page.post('/logout')
        this.$message({
          message: '您已退出登录!',
          type: 'success',
          center: true,//文字居中
        });
        // 重置身份信息
        this.$store.dispatch('userInfo/updateUser',
          {
            id: null,
            username: null,
            userInfo: {
              nickname: null,
              sex: null,
              birthday: 0,
              hometown: "['510000', '510100', '510116']",
              deliverAddress: "['510000', '510100', '510116']",// 收货地址
              addressDetail: null,// 收货详细地址
            }
          } // 保存登录后的用户信息
        )
        setTimeout(() => {
          window.location.reload()
        }, 300)
      },
      avatarClick() {
        this.$router.push({name: 'editPersonalInfo'})
      },
      tabClick(index, path = null) {
        let clickStyle = this.style.sidebar.clickStyle;
        for (let i = 0; i < clickStyle.length; i++) {
          clickStyle[i].background = null;
          clickStyle[i].color = null;
          clickStyle[i].fontWeight = null;
        }
        clickStyle[index].background = 'linear-gradient(to right,#8feb33,#34a880)';
        clickStyle[index].color = '#783acb';
        clickStyle[index].fontWeight = 'bold';
        if (path != null)
          this.$router.push(path)
      },

      computeTooltipContent() {
        let h = new Date().getHours();
        if (h > 0 && h <= 12) {
          this.tooltipContent = '早上好! ' + this.$store.state.userInfo.user.username;
        } else if (h > 12 && h < 18) {
          this.tooltipContent = '下午好! ' + this.$store.state.userInfo.user.username;
        } else {
          this.tooltipContent = '晚上好! ' + this.$store.state.userInfo.user.username;
        }
      },
    },

    beforeCreate() {
      this.$root.handleLoginAuthorization("/login")
    },

    created() {
      this.computeTooltipContent();
      switch (this.$route.name) {
        case 'userInfoShopcart':
          this.tabClick(0);
          break;
        case 'userInfoCollect':
          this.tabClick(1);
          break;
        case 'userInfoAllOrder':
          this.tabClick(2);
          break;
      }
    },
  }
</script>

<style>

  span, div, dl, dd, dt, li, ul {
    padding: 0px;
    margin: 0px;
    border: 0px;
    list-style-type: none;
  }

  .funny {
    color: aquamarine;
  }
</style>



