<template>
  <div>
    <el-form ref="registeForm" status-icon :model="registeForm" :rules="rules">
      <!--邮箱-->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.email">
          <el-form-item prop="email">
            <el-input type="text" v-model="registeForm.email" autocomplete="on" placeholder="请输入您的邮箱">
              <i slot="prefix" class="email"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!--用户名-->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.username">
          <el-form-item prop="username">
            <el-input type="text" v-model="registeForm.username" autocomplete="off" placeholder="请输入用户名">
              <i slot="prefix" class="username"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!--密码-->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.password">
          <el-form-item prop="password">
            <el-input type="password" v-model="registeForm.password" autocomplete="off" placeholder="请输入密码">
              <i slot="prefix" class="password"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!--确认密码-->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.password">
          <el-form-item prop="confirmPassword">
            <el-input type="password" v-model="registeForm.confirmPassword" autocomplete="off" placeholder="请再输入一次密码">
              <i slot="prefix" class="password"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!--验证码-->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.captcha.input">
          <el-form-item prop="captcha">
            <el-input type="text" v-model="registeForm.captcha" autocomplete="off" maxLength=4
                      placeholder="请输入验证码">
              <i slot="prefix" class="captcha"></i>
            </el-input>
          </el-form-item>
        </div>
        <img @click="getCaptcha"
             :style="style.registeForm.captcha.img"
             :src="captchaImgSrc" alt="点击重试"/>
      </div>
      <!--注册-->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.registeBtn">
          <el-button type="primary" @click="onSubmit">注册</el-button>
        </div>
      </div>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'registe',
    data() {
      let _this = this;
      var validateEmail = (rule, value, callback) => {
        _this.$page.post(_this.baseContextPath + '/checkEmail', null, {'email': _this.registeForm.email},
          response => {
            if (response.data !== true) {
              callback(new Error('此邮箱已注册,请重新输入'));
            } else {
              callback();
            }
          });
      };
      var validateUsername = (rule, value, callback) => {
        if (/^[0-9]/.test(value)) {
          callback(new Error('用户名不能以数字开头'))
        } else if (/[^a-zA-Z0-9_]/.test(value)) {
          callback(new Error('用户名只能包含字母,数字和下划线'))
        }
        else {
          _this.$page.post(_this.baseContextPath + '/checkUserName', null, {'username': _this.registeForm.username},
            response => {
              if (response.data !== true) {
                callback(new Error('此用户名已被占用,请重新输入'))
              } else {
                callback();
              }
            });
        }
      };
      var validatePassword = (rule, value, callback) => {

        if (/^[0-9]*$/.test(value)) {
          callback(new Error('密码不能全为数字'))
        } else if (/^[a-zA-Z]*$/.test(value)) {
          callback(new Error('密码不能全为字母'))
        } else {
          if (_this.registeForm.confirmPassword !== '') {//判断第二个密码框是否没有输入
            _this.$refs.registeForm.validateField('confirmPassword');
          }
          callback();
        }

      };
      var validateConfirmPassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== _this.registeForm.password) {//如果与第一个密码框的值不相等
          callback(new Error('两次输入密码不一致!'));//提示信息: 两次输入密码不一致
        } else {
          callback();
        }
      };
      var validateCaptcha = (rule, value, callback) => {
        _this.$page.post(_this.baseContextPath + '/checkCaptcha', null, {'captcha': _this.registeForm.captcha},
          response => {
            if (response.data !== true) {
              callback(new Error('验证码错误'));
            }
            else {
              _this.isCaptchaValidate = true;//验证成功后无法点击验证码
              _this.style.cursor = null;
              callback();
            }
          });
      };
      return {
        style: {
          registeForm: {
            itemBox: {
              width: this.scale(350) + 'px',
              margin: this.scale(20) + "px " + 'auto',
            },
            email: {
              verticalAlign: "middle",
            },
            username: {
              verticalAlign: "middle",
            },
            password: {
              verticalAlign: "middle",
            },
            captcha: {
              input: {
                display: 'inline-block',
                width: this.scale(200) + 'px',
                verticalAlign: "middle",
              },
              img: {
                display: 'inline-block',
                fontSize: this.scale(10) + 'px',
                verticalAlign: "middle",
                cursor: 'pointer',
                width: this.scale(100) + 'px',
                marginLeft: this.scale(10) + 'px'
              },
            },
            registeBtn: {
              margin: '0 auto',
              width: this.scale(80) + 'px'
            }
          }
        },
        registeForm: {
          email: null,
          username: null,
          password: null,
          confirmPassword: null,
          captcha: null
        },
        rules: {
          email: [{type: "email", message: '邮箱地址格式不正确', required: true, trigger: ['blur']},
            {validator: validateEmail, trigger: ['blur']}],
          username: [{required: true, message: '用户名不能为空', trigger: ['blur']},
            {validator: validateUsername, trigger: ['change', 'blur']}],
          password: [{required: true, message: '密码不能为空', trigger: ['blur']},
            {min: 6, message: '密码长度不能少于6位', trigger: ['blur']},
            {validator: validatePassword, trigger: ['change', 'blur']}],
          confirmPassword: [{required: true, message: '密码不能为空', trigger: ['blur']},
            {min: 6, message: '密码长度不能少于6位', trigger: ['blur']},
            {validator: validateConfirmPassword, trigger: ['change', 'blur']}],
          captcha: [
            {required: true, message: '请输入验证码', trigger: ['blur']},
            {validator: validateCaptcha, trigger: ['blur']}
          ],
        },
        baseContextPath: this.$route.path + '/' + this.$options.name,
        captchaImgSrc: null,//nginx中的图片路径
        isCaptchaValidate: false,
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      onSubmit() {
        let _this = this;//必须加let,否则会报错: vueThis未定义
        this.$refs.registeForm.validate((valid) => {//提交时再验证一下表单的数据(防止什么都不输入直接提交).
          if (valid) {//如果校验结果合法
            _this.$page.post(_this.baseContextPath + '/handleRegiste', null, _this.registeForm,
              response => {
                _this.$alert('请查收您的邮件,点击链接即可完成注册!', '只差最后一步', {
                  confirmButtonText: '确定',
                  callback: (action, instance) => {
                    setTimeout(function () {
                      window.location.reload()// 刷新页面，以切换到login标签页
                    }, 200)
                  }
                });
              },
              error => {
                _this.$message({
                  message: "服务器繁忙，请稍后再试!",
                  type: 'error',
                  center: true,
                });
              });
          } else {
            return false;//一定要返回false代表提交时未验证
          }
        });
      },
      getCaptcha() {
        let _this = this;
        if (!this.isCaptchaValidate) {//验证后就无法再获取验证码
          _this.$page.get(_this.baseContextPath + '/getCaptcha', null, null,
            response => {
              _this.registeForm.captcha = null
              _this.captchaImgSrc = '/temp/images/captcha/' + response.data;
            }
          );
        }
      },
    },
    mounted() {
    }
  }
</script>

<style scoped>

  .el-form-item {
    margin-bottom: 0px;
  }

  .email {
    background: url(../../assets/user/email-icon.png) center no-repeat;
    background-size: cover;
  }

  .email:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .username {
    background: url(../../assets/user/username-icon.png) center no-repeat;
    background-size: cover;
  }

  .username:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .captcha {
    background: url(../../assets/user/captcha-icon.png) center no-repeat;
    background-size: cover;
  }

  .captcha:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .password {
    background: url(../../assets/user/password-icon.png) center no-repeat;
    background-size: cover;
  }

  .password:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

</style>



