// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router/index'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import store from './store/index.js'
import promise from 'es6-promise' //使ie能够使用Promise
import $ from 'jquery/dist/jquery.min.js'
import api from './api/api.js'
import page from './api/page.js'
import authorization from './api/authorization.js'
import 'babel-polyfill'

Vue.config.productionTip = false//阻止 vue 在启动时生成生产提示
Vue.use(ElementUI);
Vue.prototype.$api = api;//访问api数据的请求，使用this.$api.get()/post()/delete()/put()发出
Vue.prototype.$page = page;//访问页面数据的请求，比如表格的数据，使用this.$page.get()/post()/delete()/put()发出
Vue.prototype.$authorization = authorization;//访问页面授权数据的请求，比如登录验证，使用this.$authorization.get()/post()/delete()/put()发出
promise.polyfill();////使ie能够使用Promise

//当路由发生变化时调用，相当于拦截器
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  // 解决IE11 有时候不跳转的问题
  if (
    '-ms-scroll-limit' in document.documentElement.style &&
    '-ms-ime-align' in document.documentElement.style
  ) { // detect it's IE11
    window.addEventListener("hashchange", function (event) {
      let currentPath = window.location.hash.slice(1);
      if (to !== currentPath) {
        router.push(currentPath)
      }
    }, false)
  }
  next();
})

/* eslint-disable no-new */
var vm = new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>',
  data() {
    return {
      beforeAuthorizationPath: null,//认证前的路径
    }
  },
  methods: {
    /**
     *  功能: 针对不同的设备对value进行缩放以适应不同分辨率
     *  注意:
     *  1. <meta name="viewport" content="width=device-width,initial-scale=0.1">
     *  2. 必须要设置顶层容器的宽度为:this.scale(1366)+'px',
     *  3. 只能使用span，不能使用响应式属性sm等。
     *  4. 在每个需要缩放的组件中定义一个scale(value){return this.$root.scale(value);}来调用这里的scale()
     *  5. 禁止使用%来设置宽度，可以设置高度.
     * @param value 被缩放的值
     * @returns {number}
     */
    scale(value) {
      //必须使用parseInt进行取整，因为el的span,push不能处理小数
      return parseInt(value * window.screen.width * window.devicePixelRatio / 1366.0)
    },
    /**
     * 需要登录权限才能访问的所有页面
     * 就在它的created()中调用:this.$root.handleLoginAuthorization("/不能访问时的跳转路径");进行权限判断
     *
     * @param redirectPath 没有权限时的重定向的路径
     */
    handleLoginAuthorization(redirectPath) {
      this.beforeAuthorizationPath = this.$route.path;//保存当前路径，当授权通过后跳回到这个路径
      let _this = this;
      $.ajax({
        type: "get",
        url: '/authorization/login',
        //url: '/authorization' + _this.$route.path,
        async: false,//必须使用同步，否则下一个组件在验证数据还未到达时就加载了。
        success: function (data) {
          _this.$store.dispatch('userInfo/updateUser', data)
        },
        error: function (data) {//如果返回的是错误的状态码,比如4**，5**
          _this.$router.push(redirectPath)
          /* if (data.status == '401') {
             _this.$router.push(redirectPath)
           } else {
             _this.$router.push('/' + data.status)//重定向到错误页面
           }*/
        }
      })
    },
  },
});





