<template>
  <div>
    <dl>
      <!--排序条件,如:综合,销量,新品,价格等-->
      <dt>
        <ul :style="style.sort.box">
          <template v-for="(sortValue,sortValueIndex) in sortValues">
            <li class="sortValue"
                @click="sortValueClick($event,sortValueIndex)"
                :style="setSortValueStyle(sortValueIndex)">
              <span :style="style.sort.sortText">
                {{sortValue.text}}
              </span>
              <span :style="setSortLabelStyle(sortValueIndex)">

              </span>
            </li>
          </template>
          <!--价格区间-->
          <li @mouseover="onMouseOverPriceInput"
              @mouseout="onMouseOutPriceInput"
              :style="style.sort.priceInput.box">
            <div :style="style.sort.priceInput.adjustHeight">
              <input :style="style.sort.priceInput.input" v-model="minPrice" type="number" placeholder="￥" min="0"
                     max="999999999"/>
              <span>-</span>
              <input :style="style.sort.priceInput.input" v-model="maxPrice" type="number" placeholder="￥" min="0"
                     max="999999999"/>
            </div>

            <div id="priceInput"
                 :style="style.sort.priceInput.clear.box">
              <span @click="cleartBtnClick"
                    :style="style.sort.priceInput.clear.cleartBtn">清空</span>
              <button @click="confirmBtnClick"
                      :style="style.sort.priceInput.clear.confirmBtn">确定
              </button>
            </div>
          </li>
          <!--上下翻页按钮-->
          <li :style="style.sort.pageUpAndDown.box">
            <span :style="style.sort.pageUpAndDown.arrow.box">
              <span class="left-right-arrow"
                    title="上一页"
                    @click="prevClick"
                    :style="style.sort.pageUpAndDown.arrow.leftArrow">◁</span>
              <span class="left-right-arrow"
                    title="下一页"
                    @click="nextClick"
                    :style="style.sort.pageUpAndDown.arrow.rightArrow">▷</span>
            </span>
            <span :style="style.sort.pageUpAndDown.page.box">
              <span
                :style="style.sort.pageUpAndDown.page.currentPage">{{this.$store.state.goodsShow.currentPage}}</span>/{{itemCount%pageSize==0?itemCount/pageSize:parseInt(itemCount/pageSize)+1}}
            </span>
          </li>
        </ul>
      </dt>
      <!--匹配的商品-->
      <dd>
        <ul :style="style.goodsParent.box">
          <template v-if="pagiGoodsParent != null && pagiGoodsParent.length>0">
            <template v-for="(goodsParentItem,goodsParentItemIndex) in pagiGoodsParent">
              <li class="goods-item"
                  @click="goodsItemClick(goodsParentItem.id)"
                  :title="goodsParentItem.title"
                  :style="style.goodsParent.item.box">
                <img :src="goodsParentItem.imgSrc"
                     :style="style.goodsParent.item.img">
                <div :style="style.goodsParent.item.price.box">
                  <!--打折价-->
                  <span class="clear-space">
                  <span :style="style.goodsParent.item.price.moneySymbol">¥</span>
                  <span :style="style.goodsParent.item.price.discount">{{goodsParentItem.discount}}</span>
                </span>
                  <!--原价-->
                  <span class="clear-space"
                        :style="style.goodsParent.item.price.originalPrice.box">
                  <del :style="style.goodsParent.item.price.originalPrice.text">¥</del>
                  <del :style="style.goodsParent.item.price.originalPrice.text">{{goodsParentItem.originalPrice}}</del>
                </span>
                  <!--折扣-->
                  <template v-if="goodsParentItem.discount != goodsParentItem.originalPrice">
                   <span :style="style.goodsParent.item.price.discountRate">
                     {{(goodsParentItem.discount/goodsParentItem.originalPrice * 10).toFixed(1)}}折
                   </span>
                  </template>
                </div>
                <div :style="style.goodsParent.item.title.box">
                  <!--商品标题-->
                  <span class="goods-title"
                        :style="style.goodsParent.item.title.text">
                  {{goodsParentItem.title}}
                </span>
                </div>
                <!--月成交量,评论数-->
                <div :style="style.goodsParent.item.monthSaleAndEvaluateNum.box">
                <span :style="style.goodsParent.item.monthSaleAndEvaluateNum.monthSale.box">
                  月成交
                  <span :style="style.goodsParent.item.monthSaleAndEvaluateNum.monthSale.text">{{goodsParentItem.monthSaleCount}}笔</span>
                </span>
                  <span :style="style.goodsParent.item.monthSaleAndEvaluateNum.evaluate.box">
                  <img src="../../assets/goods/evaluate.png"
                       :style="style.goodsParent.item.monthSaleAndEvaluateNum.evaluate.img"/>
                  <span :style="style.goodsParent.item.monthSaleAndEvaluateNum.evaluate.text">{{goodsParentItem.evaluateCount}}</span>
                </span>
                </div>
              </li>
            </template>
          </template>
        </ul>
      </dd>
      <!--底部的翻页按钮-->
      <dd>
        <div :style="style.pagination.flexBox">
          <el-pagination
            :total="this.$store.state.goodsShow.itemCount"
            :current-page="this.$store.state.goodsShow.currentPage"
            :page-size="this.$store.state.goodsShow.pageSize"
            layout="total,  prev, pager, next, jumper"
            @current-change="handleCurrentPageChange"
          >
          </el-pagination>
        </div>
      </dd>
    </dl>
  </div>
</template>

<script>
  export default {
    name: "goodsShow",
    props: {
      pagiGoodsParent: {
        type: Array
      },
    },
    computed: {
      minPrice: {
        get() {
          return this.$store.state.goodsShow.minPrice
        },
        set(value) {
          this.$store.commit('goodsShow/updateMinPrice', value)
        }
      },
      maxPrice: {
        get() {
          return this.$store.state.goodsShow.maxPrice
        },
        set(value) {
          this.$store.commit('goodsShow/updateMaxPrice', value)
        }
      },
      itemCount: {
        get() {
          return this.$store.state.goodsShow.itemCount;
        }
      },
      pageSize: {
        get() {
          return this.$store.state.goodsShow.pageSize;
        }
      }
    },
    data() {
      return {
        style: {
          sort: {
            box: {
              marginTop: this.scale(20) + 'px',
              width: '100%',
              fontSize: 0,
              display: 'inline-block',
            },
            sortText: {
              verticalAlign: 'middle',
            },
            priceInput: {
              box: {
                display: 'inline-block',
                border: '1px solid white',
                fontSize: this.scale(15) + 'px',
                backgroundColor: 'white',
                minHeight: this.scale(23) + 'px',
                lineHeight: this.scale(23) + 'px',
                padding: this.scale(10) + 'px ' + this.scale(20) + 'px',
                verticalAlign: 'top',
                position: 'absolute',
                zIndex: 5,
              },
              adjustHeight: {
                height: this.scale(23) + 'px',
              },
              input: {
                width: this.scale(60) + 'px',
                height: this.scale(18) + 'px',
              },
              clear: {
                box: {
                  marginTop: this.scale(10) + 'px',
                  display: 'none',
                  fontSize: this.scale(12) + 'px',
                },
                cleartBtn: {
                  color: '#51b8f1',
                  cursor: 'pointer',
                  verticalAlign: 'middle',
                },
                confirmBtn: {
                  cursor: 'pointer',
                  color: 'white',
                  backgroundColor: '#e31436',
                  float: 'right',
                  fontSize: this.scale(12) + 'px',
                }
              },
            },
            pageUpAndDown: {
              box: {
                // width: this.scale(1100 - (4 * (72 + 1) + 146 + 1 + 20 * 2 + 1 * 2)) + 'px',
                width: this.scale(600) + 'px',
                float: 'right',
                display: 'inline-block',
                fontSize: this.scale(25) + 'px',
                position: 'relative',
                border: '1px solid white',
                backgroundColor: 'white',
                padding: this.scale(10) + 'px ' + this.scale(20) + 'px',
                height: this.scale(23) + 'px',
                lineHeight: this.scale(23) + 'px',
              },
              page: {
                box: {
                  float: 'right',
                  fontSize: this.scale(14) + 'px',
                  verticalAlign: 'middle',
                },
                currentPage: {
                  color: 'red'
                }
              },
              arrow: {
                box: {
                  float: 'right',
                  verticalAlign: 'middle',
                },
                leftArrow: {
                  marginRight: this.scale(20) + 'px',
                  marginLeft: this.scale(20) + 'px',
                },
                rightArrow: {}
              },
            },
          },
          goodsParent: {
            box: {
              marginTop: this.scale(20) + 'px',
            },
            item: {
              box: {
                display: 'inline-block',
                marginBottom: this.scale(15) + 'px',
                marginRight: this.scale((1100 - (230 + 10 * 2 + 1 * 2) * 4) / 4) + 'px',
                backgroundColor: 'white',
                padding: this.scale(10) + 'px',
              },
              img: {
                width: this.scale(230) + 'px',
                height: this.scale(230) + 'px',
              },
              price: {
                box: {
                  marginTop: this.scale(10) + 'px',
                },
                moneySymbol: {
                  fontSize: this.scale(12) + 'px',
                  color: 'rgb(227, 20, 54)',
                },
                discount: {
                  fontSize: this.scale(20) + 'px',
                  fontWeight: this.scale(700),
                  color: 'rgb(227, 20, 54)',
                },
                originalPrice: {
                  box: {
                    marginLeft: this.scale(10) + 'px',
                  },
                  text: {
                    color: '#999',
                    whiteSpace: 'normal',
                    lineHeight: this.scale(22) + 'px',
                    fontSize: this.scale(12) + 'px',
                  }
                },
                discountRate: {
                  marginLeft: this.scale(10) + 'px',
                  border: '1px solid red',
                  fontSize: this.scale(8) + 'px',
                  color: 'red',
                  float: 'right',
                }
              },
              title: {
                box: {
                  marginTop: this.scale(5) + 'px',
                },
                text: {
                  fontSize: this.scale(13) + 'px',
                  whiteSpace: 'nowrap',
                  display: 'inline-block',
                  width: this.scale(230) + 'px',
                  textOverflow: 'ellipsis',
                  overflow: 'hidden',//只显示一行，使文字不溢出
                }
              },
              monthSaleAndEvaluateNum: {
                box: {
                  marginTop: this.scale(5) + 'px',
                  fontSize: this.scale(12) + 'px',
                },
                monthSale: {
                  box: {
                    float: 'left',
                  },
                  text: {
                    color: '#b57c5b',
                  }
                },
                evaluate: {
                  box: {
                    float: 'right',
                  },
                  img: {
                    verticalAlign: 'middle',
                  },
                  text: {
                    verticalAlign: 'middle',
                  }
                }
              }
            }

          },
          pagination: {
            flexBox: {
              display: 'flex',
              'flex-direction': 'row',
              'justify-content': 'center',
              paddingTop: this.scale(40) + 'px',
              paddingBottom: this.scale(100) + 'px',
            },
          }
        },// style end
        /*orderBy: 'category_id',
        minPrice: "",
        maxPrice: "",*/
        sortValues: [
          {
            text: '综合',
            orderByName: 'category_id',
            desc: true
          },
          {
            text: '销量',
            orderByName: 'month_sale_count',
            desc: true
          },
          {
            text: '新品',
            orderByName: 'add_datetime',
            desc: true
          },
          {
            text: '价格',
            orderByName: 'discount',
            desc: false
          }
        ]
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      /**
       * 处理当前页发生改变
       */
      handleCurrentPageChange(currentPage) {
        this.$store.commit("goodsShow/updateCurrentPage", currentPage)
        this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory();
      },

      /**
       * el分页点击上一页
       */
      elPrevClick(currentPage) {
        /*this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory(
          this.$route.query.categoryId,
          this.$route.query.searchValue,
          this.$store.state.goodsShow.orderBy,
          currentPage);*/
      },

      /**
       * el分页点击下一页
       */
      elNextClick(currentPage) {
        /*this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory(
          this.$route.query.categoryId,
          this.$route.query.searchValue,
          this.$store.state.goodsShow.orderBy,
          currentPage);*/
      },

      /**
       * 筛选处点击上一页
       */
      prevClick() {
        let currentPage = this.$store.state.goodsShow.currentPage - 1;
        if (currentPage > 0) {
          this.$store.commit("goodsShow/updateCurrentPage", currentPage)
          this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory();
        }
      },

      /**
       * 筛选处点击下一页
       */
      nextClick() {
        let currentPage = this.$store.state.goodsShow.currentPage + 1;
        if (currentPage <= (this.itemCount % this.pageSize == 0 ? this.itemCount / this.pageSize : parseInt(this.itemCount / this.pageSize) + 1)) {
          this.$store.commit("goodsShow/updateCurrentPage", currentPage)
          this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory();
        }
      },

      setSortValueStyle(sortValueIndex) {
        if (sortValueIndex === 0) {
          return {
            display: 'inline-block',
            marginRight: '1px',
            backgroundColor: 'white',
            border: '1px solid #e31436',
            fontSize: this.scale(15) + 'px',
            padding: this.scale(10) + 'px ' + this.scale(20) + 'px',
            verticalAlign: 'top',
            height: this.scale(23) + 'px',
            lineHeight: this.scale(23) + 'px',
            color: '#e31436',
            cursor: 'pointer',
          }
        } else {
          return {
            display: 'inline-block',
            border: '1px solid white',
            marginRight: '1px',
            backgroundColor: 'white',
            fontSize: this.scale(15) + 'px',
            padding: this.scale(10) + 'px ' + this.scale(20) + 'px',
            verticalAlign: 'top',
            height: this.scale(23) + 'px',
            lineHeight: this.scale(23) + 'px',
            cursor: 'pointer',
          }
        }
      },

      setSortLabelStyle(sortValueIndex) {
        if (this.sortValues[sortValueIndex].desc) {
          return {
            display: 'inline-block',
            backgroundImage: "url(" + require("../../assets/goods/goodsShow/arrow up.png") + ")",
            backgroundSize: this.scale(20) + 'px ' + this.scale(20) + 'px',
            backgroundRepeat: 'no-repeat',
            height: this.scale(20) + 'px',
            width: this.scale(20) + 'px',
            verticalAlign: 'middle',
          }
        } else {
          return {
            display: 'inline-block',
            backgroundImage: "url(" + require("../../assets/goods/goodsShow/arrow down.png") + ")",
            backgroundSize: this.scale(20) + 'px ' + this.scale(20) + 'px',
            backgroundRepeat: 'no-repeat',
            height: this.scale(20) + 'px',
            width: this.scale(20) + 'px',
            verticalAlign: 'middle',
          }
        }
      },

      sortValueClick(event, sortValueIndex) {
        if (this.sortValues[sortValueIndex].desc) {
          this.$store.commit("goodsShow/updateOrderBy", this.sortValues[sortValueIndex].orderByName + " desc")
          this.sortValues[sortValueIndex].desc = false;
        } else {
          this.$store.commit("goodsShow/updateOrderBy", this.sortValues[sortValueIndex].orderByName)
          this.sortValues[sortValueIndex].desc = true;
        }
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        /*if (this.$store.state.goodsShow.navValue == "") {
          this.$parent.selectAllGoodsParent();
        } else {
          this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory();
        }*/
        this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory();

        let sortValues = document.querySelectorAll(".sortValue");
        sortValues.forEach(sortValue => {
          sortValue.style.border = '1px solid white';
          sortValue.style.color = 'black';
        })
        event.currentTarget.style.border = '1px solid #e31436';
        event.currentTarget.style.color = '#e31436';
      },

      goodsItemClick(goodsParentId) {
        this.$router.push({name: 'goodsDetail', params: {goodsParentId: goodsParentId}})
      },

      onMouseOverPriceInput(event) {
        let priceInput = document.querySelector('#priceInput');
        priceInput.style.display = 'block';
      },

      onMouseOutPriceInput(event) {
        let priceInput = document.querySelector('#priceInput');
        priceInput.style.display = 'none';
      },

      cleartBtnClick() {
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        this.$store.commit("goodsShow/updateMinPrice", 0)
        this.$store.commit("goodsShow/updateMaxPrice", 999999999)
        this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory();
      },

      confirmBtnClick() {
        if (this.minPrice >= 0 && this.maxPrice <= 999999999 && this.minPrice <= this.maxPrice) {
          this.$store.commit("goodsShow/updateCurrentPage", 1);
          this.$parent.getGoodsParentAndUserSelectedGoodsDetailCategory();
        }
      }
    }
  }
</script>

<style scoped>
  dl, dt, dd, ul, ol, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .clear-space {
    font-size: 0;
  }

  .goods-item {
    border: 1px solid white;
  }

  .goods-item:hover {
    border: 1px solid #e31436;
    cursor: pointer;
  }

  .goods-title {
    text-decoration: none;
    color: #000;
  }

  .goods-title:hover {
    text-decoration: underline;
    cursor: pointer;
    color: #e31436
  }

  .left-right-arrow:hover {
    cursor: pointer;
    color: #e31436
  }

</style>



