<template>
  <div :style="style.container">
    <template v-for="(homeGoodsItem,row) in homeGoods">
      <div :style="style.titleBox">
        <div>{{homeGoodsItem.title}}</div>
      </div>
      <div :style="style.centerContainer">
        <div :style="style.table">
          <div v-for="(goodsItem,col) in homeGoodsItem.goodsParentList"
               :style="style.goods.grid"
               @click="goodsClick(goodsItem.id)"
               @mouseover="onGridHover('hoverBox','unHoverBox',row,col)"
               @mouseleave="onGridUnHover('hoverBox','unHoverBox',row,col)">
            <!--hover时的每一个格子-->
            <div style="position: relative">
              <div :id="'hoverBox'+row+col"
                   :style="style.goods.hoverBox">
                <!--商品图片-->
                <div>
                  <!--商品左上角的标签-->
                  <div v-if="goodsItem.leftTopLabel" :style="style.goods.goodsImg.leftTopLabel">
                    <div>{{goodsItem.leftTopLabel}}</div>
                  </div>
                  <!-- 商品图片 -->
                  <a>
                    <img :style="style.goods.goodsImg.img" :src="goodsItem.imgSrc">
                  </a>
                </div>
                <!--商品图片底部的部分-->
                <div>
                  <!--商品标题-->
                  <span class="goods-title" :style="style.goods.bottomOfGoodsImg.hoverGoodsTitle">
                    <a href="#">{{goodsItem.title}}</a>
                  </span>
                  <!--商品格子中部的标签-->
                  <div :style="style.goods.bottomOfGoodsImg.middleLabel">
                    {{goodsItem.middleLabel}}
                  </div>
                  <!--商品底部-->
                  <div :style="style.goods.bottomOfGoodsImg.bottom.box">
                    <!--打折价-->
                    <span style="font-size: 0">
                    <span :style="style.goods.bottomOfGoodsImg.bottom.moneySymbol">¥</span>
                    <span :style="style.goods.bottomOfGoodsImg.bottom.discount">{{goodsItem.discount}}</span>
                  </span>
                    <!--原价-->
                    <span style="font-size: 0">
                      <del :style="style.goods.bottomOfGoodsImg.bottom.originalPrice">¥</del>
                      <del
                        :style="style.goods.bottomOfGoodsImg.bottom.originalPrice">{{goodsItem.originalPrice}}</del>
                    </span>
                    <a class="buy" href="#" :style="style.goods.bottomOfGoodsImg.bottom.buy">立即购买</a>
                  </div>
                </div>
              </div>
            </div>
            <!--未hover时的每一个格子-->
            <div :id="'unHoverBox'+row+col"
                 :style="style.goods.unHoverBox">
              <!--商品图片-->
              <div>
                <!--商品左上角的标签-->
                <div v-if="goodsItem.leftTopLabel" :style="style.goods.goodsImg.leftTopLabel">
                  <div>{{goodsItem.leftTopLabel}}</div>
                </div>
                <!-- 商品图片 -->
                <a>
                  <img :style="style.goods.goodsImg.img" :src="goodsItem.imgSrc">
                </a>
              </div>
              <!--商品图片底部的部分-->
              <div>
                <!--商品标题-->
                <span class="goods-title"
                      :style="style.goods.bottomOfGoodsImg.unHoverGoodsTitle">
                  <a href="#">{{goodsItem.title}}</a>
                </span>
                <!--商品格子中部的标签-->
                <div :style="style.goods.bottomOfGoodsImg.middleLabel">
                  {{goodsItem.middleLabel}}
                </div>
                <!--商品底部-->
                <div :style="style.goods.bottomOfGoodsImg.bottom.box">
                  <!--打折价-->
                  <span style="font-size: 0">
                    <span :style="style.goods.bottomOfGoodsImg.bottom.moneySymbol">¥</span>
                    <span :style="style.goods.bottomOfGoodsImg.bottom.discount">{{goodsItem.discount}}</span>
                  </span>
                  <!--原价-->
                  <span style="font-size: 0">
                      <del :style="style.goods.bottomOfGoodsImg.bottom.originalPrice">¥</del>
                      <del
                        :style="style.goods.bottomOfGoodsImg.bottom.originalPrice">{{goodsItem.originalPrice}}</del>
                    </span>
                  <a class="buy" href="#" :style="style.goods.bottomOfGoodsImg.bottom.buy">立即购买</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  export default {
    name: "hx-goods-table",
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      onGridHover(hoverBoxId, unHoverBoxId, row, col) {
        if (!this.isGridHover) {
          this.isGridHover = true;
          let hoverBox = document.querySelector('#' + hoverBoxId + row + col);
          let unHoverBox = document.querySelector('#' + unHoverBoxId + row + col);
          hoverBox.style.visibility = 'visible';
          unHoverBox.style.visibility = 'hidden';
        }
      },

      onGridUnHover(hoverBoxId, unHoverBoxId, row, col) {
        if (this.isGridHover) {
          this.isGridHover = false;
          let hoverBox = document.querySelector('#' + hoverBoxId + row + col);
          let unHoverBox = document.querySelector('#' + unHoverBoxId + row + col);
          hoverBox.style.visibility = 'hidden';
          unHoverBox.style.visibility = 'visible';
        }
      },

      goodsClick(goodsParentId) {
        this.$router.push({name: 'goodsDetail', params: {goodsParentId: goodsParentId}})
      },

    },
    data() {
      return {
        style: {
          container: {
            width: this.scale(1220) + 'px',
            backgroundColor: '#f0f0f0',
            margin: '0 auto',
          },
          titleBox: {
            backgroundColor: 'rgb(227, 20, 54)',
            height: this.scale(60) + 'px',
            lineHeight: this.scale(60) + 'px',
            fontSize: this.scale(28) + 'px',
            color: '#fff',
            textAlign: 'center',
          },
          centerContainer: {
            display: 'flex',
            justifyContent: 'center',
          },
          table: {
            width: this.scale(1130) + 'px',//一行中格子的总宽度。不能少，否则不会居中
            display: 'flex',
            flexWrap: 'wrap'
          },
          goods: {
            grid: {
              cursor: 'pointer',
              width: this.scale(200) + 'px',
              padding: this.scale(10) + 'px',// 用来显示格子hover时的边框
              position: 'relative',
            },
            unHoverBox: {// 未hover时的格子的实际显示区域
              background: 'white',
              padding: this.scale(4) + 'px',
              border: this.scale(1) + 'px solid #fff',
              visibility: 'visible',
            },
            hoverBox: {// hover时的格子的实际显示区域
              background: 'white',
              padding: this.scale(4) + 'px',
              visibility: 'hidden',
              border: this.scale(1) + 'px solid #ff4d51',
              position: 'absolute',
              zIndex: 1
            },
            goodsImg: {
              leftTopLabel: {
                position: 'absolute',
                width: this.scale(32) + 'px',
                height: this.scale(20) + 'px',
                lineHeight: this.scale(20) + 'px',
                backgroundColor: '#ff8385',
                color: '#fff',
                fontSize: this.scale(12) + 'px',
                textAlign: 'center',
              },
              img: {
                display: 'block',
                width: this.scale(192) + 'px',
                height: this.scale(192) + 'px',
              }
            },
            bottomOfGoodsImg: {
              unHoverGoodsTitle: {
                fontSize: this.scale(14) + 'px',
                fontWeight: this.scale(700),
                whiteSpace: 'nowrap',
                display: 'inline-block',
                width: this.scale(192) + 'px',
                textOverflow: 'ellipsis',
                overflow: 'hidden',//只显示一行，使文字不溢出
              },
              hoverGoodsTitle: {
                fontSize: this.scale(14) + 'px',
                fontWeight: this.scale(700),
              },
              middleLabel: {
                marginTop: this.scale(6) + 'px',
                color: '#666',
                fontSize: this.scale(12) + 'px',
              },
              bottom: {
                box: {
                  marginTop: this.scale(5) + 'px',
                },
                moneySymbol: {
                  fontSize: this.scale(12) + 'px',
                  color: 'rgb(227, 20, 54)',
                },
                discount: {
                  fontSize: this.scale(20) + 'px',
                  fontWeight: this.scale(700),
                  color: 'rgb(227, 20, 54)',
                },
                originalPrice: {
                  color: '#999',
                  whiteSpace: 'normal',
                  lineHeight: this.scale(22) + 'px',
                  fontSize: this.scale(12) + 'px',
                },
                buy: {
                  display: 'inline-block',
                  height: this.scale(22) + 'px',
                  padding: '0 ' + this.scale(10) + 'px',
                  borderRadius: this.scale(3) + 'px',
                  background: '#e31436',
                  color: '#fff',
                  lineHeight: this.scale(22) + 'px',
                  textDecoration: 'none',
                  fontSize: this.scale(12) + 'px',
                  float: 'right',
                }
              }
            }
          }
        },// style end
        isGridHover: false,
        homeGoods: [],
      }
    },

    created() {
      this.$page.get("goods/homeGoods", null, null,
        response => {
          this.homeGoods = response.data;
        })
    },

  }
</script>

<style scoped>
  .goods-title a {
    text-decoration: none;
    color: #000;
  }

  .goods-title a:hover {
    text-decoration: underline;
  }

  .buy:hover {
    opacity: .8;
  }
</style>




