<template>
  <div>
    <!--其他用户对商品的评价-->
    <div :style="style.otherEvaluate.box">
      <span :style="style.otherEvaluate.text">其他用户评价：</span>
      <el-rate
        :style="style.otherEvaluate.rate"
        disabled
        v-model="evaluateAvgScore"
        :colors="['#99A9BF', '#F7BA2A', '#FF9900']"
        show-score=""
        text-color="red"
        score-template="{value}">
      </el-rate>
    </div>
    <dl>
      <!--全部评价,有图,追评-->
      <dt :style="style.title">
        <el-radio-group v-model="titleRadioChoose" @change="handleViewEvaluateType">
          <el-radio label="0">
            全部&nbsp
            <span>
              {{evaluateCount}}
            </span>
          </el-radio>
          <el-radio label="1">
            有图&nbsp
            <span>
              {{haveImgCount}}
            </span>
          </el-radio>
          <el-radio label="2">
            追评&nbsp
            <span>
              {{addEvaluateCount}}
            </span>
          </el-radio>
        </el-radio-group>
      </dt>
      <dd>
        <!--每一个用户的评价-->
        <ul>
          <li v-for="(userEvaluate,userEvaluateIndex) in userEvaluates"
              :style="[style.content.user.item,setUserItemStyle(userEvaluates,userEvaluateIndex)]">
            <!--第一行-->
            <div class="clear-space">
              <!--用户头像-->
              <span :style="[style.content.user.img,setUserAvatarSrc(userEvaluate.avatarSrc)]"/>
              <!--用户第一次文字评价-->
              <span :style="style.content.user.text">{{userEvaluate.goodsEvaluateList.filter(v=>v.evaluateNum===0)[0].evaluateText}}</span>
              <!--商品选项-->
              <ul :style="style.content.user.goodsOptions.box">
                <li v-for="goodsOptionValue,goodsOptionName in JSON.parse(userEvaluate.goodsOptions)"
                    :style="style.content.user.goodsOptions.item">
                  {{goodsOptionName}}:&nbsp&nbsp&nbsp&nbsp{{goodsOptionValue}}
                </li>
              </ul>
              <!--用户第一次评价的时间-->
              <span :style="style.content.user.evaluateTime">
                {{new Date(userEvaluate.goodsEvaluateList.filter(v=>v.evaluateNum===0)[0].evaluateTime).toISOString().replace(/T.*/,"")}}
              </span>
            </div>
            <!--第二行,图片评价-->
            <div :style="style.content.user.secondRow.box">
              <template
                v-for="evaluateImg in userEvaluate.goodsEvaluateList.filter(v=>v.evaluateNum===0)[0].goodsEvaluateImgList">
                <a :href="evaluateImg.evaluateImgSrc" title="点击查看大图" target="_Blank">
                  <img :style="style.content.user.secondRow.evaluateImg"
                       :src="evaluateImg.evaluateImgSrc">
                </a>
              </template>
            </div>
            <!--第三行,追评-->
            <template v-for="notFirstEvaluate in getNotFirstEvaluates(userEvaluate)">
              <div :style="style.content.user.thirdRow.box">
                <span :style="style.content.user.thirdRow.addEvaluateText">
                  <template
                    v-if="getIntervalDays(userEvaluate.goodsEvaluateList.filter(v=>v.evaluateNum===0)[0].evaluateTime,notFirstEvaluate.evaluateTime)!=0">
                    {{getIntervalDays(userEvaluate.goodsEvaluateList.filter(v=>v.evaluateNum===0)[0].evaluateTime,notFirstEvaluate.evaluateTime)}}天后追加
                  </template>
                  <template v-else>
                    当天追加
                  </template>
                </span>
                <!--文字评价-->
                <span :style="style.content.user.thirdRow.text">{{notFirstEvaluate.evaluateText}}</span>
                <!--图片评价-->
                <div :style="style.content.user.thirdRow.evaluateImg.box">
                  <template v-for="goodsEvaluateImg in notFirstEvaluate.goodsEvaluateImgList">
                    <a :href="goodsEvaluateImg.evaluateImgSrc" title="点击查看原图" target="_Blank">
                      <img :style="style.content.user.thirdRow.evaluateImg.img"
                           :src="goodsEvaluateImg.evaluateImgSrc">
                    </a>
                  </template>
                </div>
              </div>
            </template>
          </li>
        </ul>
      </dd>
    </dl>
  </div>
</template>

<script>
  export default {
    name: "goodsEvaluate",
    components: {},
    props: {
      goodsParentId: {},
      evaluateAvgScore: {},
      evaluateCount: {},
      haveImgCount: {},
      addEvaluateCount: {}
    },

    data() {
      return {
        style: {
          otherEvaluate: {
            box: {
              height: this.scale(90) + 'px',
              lineHeight: this.scale(90) + 'px',
              width: this.scale(300) + 'px',
              margin: "0 auto",
            },
            text: {
              color: '#d61640',
              fontWeight: 'bold',
              verticalAlign: 'middle',
            },
            rate: {
              display: 'inline-block',
              verticalAlign: 'middle',
              fontWeight: 'bold',
            }
          },
          title: {
            paddingLeft: this.scale(60) + 'px',
            height: this.scale(60) + 'px',
            lineHeight: this.scale(60) + 'px',
            backgroundColor: '#f8f8f8',
            borderTop: this.scale(1) + 'px solid #eaeaea',
            borderBottom: this.scale(1) + 'px solid #eaeaea'
          },
          content: {
            user: {
              item: {
                paddingLeft: this.scale(30) + 'px',
                paddingTop: this.scale(10) + 'px',
                paddingBottom: this.scale(20) + 'px',
                borderBottom: this.scale(1) + 'px solid #ccc',
              },
              img: {
                display: 'inline-block',
                height: this.scale(40) + 'px',
                width: this.scale(40) + 'px',
                backgroundPosition: 'center',
                backgroundRepeat: 'no-repeat',
                verticalAlign: 'middle',
                borderRadius: '50%'
              },
              text: {
                display: 'inline-block',
                verticalAlign: 'middle',
                fontSize: this.scale(13) + 'px',
                marginLeft: this.scale(20) + 'px',
                width: this.scale(550) + 'px',
                color: '#333'
              },
              goodsOptions: {
                box: {
                  display: 'inline-block',
                  marginLeft: this.scale(30) + 'px',
                  fontSize: this.scale(13) + 'px',
                  verticalAlign: 'middle',
                  color: '#999',
                },
                item: {
                  marginTop: this.scale(5) + 'px',
                  marginBottom: this.scale(5) + 'px',
                  width: this.scale(200) + 'px',
                }
              },
              evaluateTime: {
                display: 'inline-block',
                marginRight: this.scale(20) + 'px',
                fontSize: this.scale(12) + 'px',
                float: 'right',
                color: '#999',
              },
              secondRow: {
                box: {
                  marginTop: this.scale(10) + 'px',
                  marginLeft: this.scale(60) + 'px',
                  width: this.scale(500) + 'px'
                },
                evaluateImg: {
                  width: this.scale(70) + 'px',
                  height: this.scale(70) + 'px',
                  marginRight: this.scale(20) + 'px',
                },
              },
              thirdRow: {
                box: {
                  borderTop: this.scale(1) + 'px dashed #ccc',
                  marginTop: this.scale(10) + 'px',
                  marginLeft: this.scale(60) + 'px',
                  width: this.scale(550) + 'px'
                },
                addEvaluateText: {
                  fontSize: this.scale(13) + 'px',
                  color: '#999',
                },
                text: {
                  marginTop: this.scale(10) + 'px',
                  marginLeft: this.scale(20) + 'px',
                  display: 'inline-block',
                  fontSize: this.scale(13) + 'px',
                  width: this.scale(550) + 'px',
                  color: '#333'
                },
                evaluateImg: {
                  box: {
                    marginLeft: this.scale(20) + 'px',
                    marginTop: this.scale(10) + 'px',
                    width: this.scale(500) + 'px'
                  },
                  img: {
                    width: this.scale(70) + 'px',
                    height: this.scale(70) + 'px',
                    marginRight: this.scale(20) + 'px',
                  },
                }
              }
            }
          }
        },// style end
        titleRadioChoose: '0',// 用户选择的是'全部',还是'有图',还是'追评'
        userEvaluates: [// 每一个用户的评价
          {
            avatarSrc: '',
            goodsOptions: "{}",// json字符串,从order表的goods_options字段得到
            goodsEvaluateList: [
              {
                evaluateText: "",
                evaluateTime: 0,
                evaluateNum: 0,
                goodsEvaluateImgList: [
                  {evaluateImgSrc: ""}
                ]
              }
            ]
          },
        ]
      }
    }
    ,
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      setUserAvatarSrc(avatarSrc) {
        return {
          backgroundImage: "url(" + avatarSrc + ")",
        }
      },
      /**
       * 处理查看不同的评价类型（全部，图片，追评）
       * @param label 被点击的按钮索引
       */
      handleViewEvaluateType(label) {
        function getEvaluateType(label) {
          let evaluateTypeMap = {
            '0': function () {// 当点击全部
              //console.log(label)
            },
            '1': function () {// 当点击有图
              // console.log(label)
            },
            '2': function () {// 当点击追评
              // console.log(label)
            },
          }
          return evaluateTypeMap[label]()
        }

        getEvaluateType(label)
      },
      setUserItemStyle(userEvaluates, userEvaluateIndex) {
        if (userEvaluateIndex === userEvaluates.length - 1) {// 删除最后一个item的下边框
          return {borderBottom: null}
        }
      },
      /* /!**
        * 获取第一次评价的有关信息
        * @param userEvaluate
        * @returns {T}
        *!/
       getFirstEvaluate(userEvaluate) {
         let firstEvaluate = userEvaluate.evaluates.find((v) => {
           return v.evaluateNum === 0
         })
         return firstEvaluate;
       },*/
      /**
       * 获取非第一次评价的有关信息
       * @param userEvaluate
       * @returns {T}
       */
      getNotFirstEvaluates(userEvaluate) {
        let notFirstEvaluates = userEvaluate.goodsEvaluateList.filter((v) => {
          return v.evaluateNum > 0
        })
        return notFirstEvaluates;
      },
      /**
       * 计算两个日期相差的天数
       * @param firstEvaluateTime
       * @param currentEvaluateTime
       * @returns {number}
       */
      getIntervalDays(firstEvaluateTime, currentEvaluateTime) {
        let d1 = new Date(firstEvaluateTime);
        let d2 = new Date(currentEvaluateTime);
        let msToDay = 1000 * 60 * 60 * 24;// ms转换为天数的进制
        let days = parseInt((d2.getTime() - d1.getTime()) / msToDay);
        return days;
      },

      getGoodsEvaluate() {
        this.$page.get("/goods/getGoodsEvaluate", {goodsParentId: this.goodsParentId}, null,
          response => {
            this.userEvaluates = response.data;
          })
      }
    },
    mounted() {
      this.getGoodsEvaluate();
    }
  }
</script>

<style scoped>
  dl, dt, dd, ul, ol, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .clear-space {
    font-size: 0;
  }
</style>



