<template>
  <div>
    <!--商品参数-->
    <ul :style="style.parameter.ul">
      <template v-for="(goodsParameterNameItem,goodsParameterNameIndex) in goodsParameterNames"
                v-if="goodsParameterNameIndex < goodsParameterCountOfRow*2 "><!--只显示前两行参数-->
        <li :style="style.parameter.li.box">
          <!--商品参数名称-->
          <span :style="style.parameter.li.goodsParameterName">
          {{goodsParameterNameItem.goodsParameterName}}:
        </span>
          <!--商品参数值-->
          <span :style="style.parameter.li.goodsParameterValue">
          {{goodsParameterValues['parameterValue'+goodsParameterNameIndex]}}
        </span>
        </li>
      </template>
      <template v-else>
        <li :style="style.parameter.li.box"
            v-if="goodsParameterShow">
          <!--商品参数名称-->
          <span :style="style.parameter.li.goodsParameterName">
            {{goodsParameterNameItem.goodsParameterName}}:
          </span>
          <!--商品参数值-->
          <span :style="style.parameter.li.goodsParameterValue">
            {{goodsParameterValues['parameterValue'+goodsParameterNameIndex]}}
          </span>
        </li>
      </template>
      <!--展开,收起按钮-->
      <template v-if="goodsParameterNames.length >= goodsParameterCountOfRow*2">
        <div @click="showParameterBoxClick"
             :style="style.parameter.showParameterClickBox">
          <div v-if="!goodsParameterShow">
            展开&nbsp&nbsp<i class="el-icon-arrow-down"></i>
          </div>
          <div v-else>
            收起&nbsp&nbsp<i class="el-icon-arrow-up"></i>
          </div>
        </div>
      </template>
    </ul>
    <!--商品介绍图片-->
    <div :style="style.goodsIntroduceImgs.box">
      <img :style="style.goodsIntroduceImgs.img"
           v-for="goodsIntroduceImg in goodsIntroduceImgs"
           :src="goodsIntroduceImg.imgSrc">
    </div>
  </div>
</template>

<script>
  export default {
    name: "goodsIntroduce",
    props: {
      // 整个标签页的宽度，默认300px
      tabWidth: {
        type: Number,
      },
      goodsParentId: {}
    },
    data() {
      return {
        style: {
          parameter: {
            ul: {
              width: this.scale(this.tabWidth - 60) + 'px',
              margin: '0 auto',
              marginTop: this.scale(40) + 'px',
            },
            li: {
              box: {
                display: 'inline-block',
                margin: this.scale(5) + 'px ' + this.scale(20) + 'px ',
                width: this.scale((this.tabWidth - 60 - 20 * 2 * this.goodsParameterCountOfRow) / this.goodsParameterCountOfRow) + 'px',// 60代表ul两边的margin大小，20*2代表li.box两边的margin
                fontSize: this.scale(12) + 'px',
                verticalAlign: "top",
              },
              goodsParameterName: {
                marginRight: this.scale(5) + 'px',
                fontSize: 'inherit',
              },
              goodsParameterValue: {
                fontSize: 'inherit',
              }
            },
            showParameterClickBox: {
              width: this.scale(50) + 'px',
              height: this.scale(20) + 'px',
              marginLeft: this.scale(20) + 'px',
              marginTop: this.scale(10) + 'px',
              cursor: 'pointer',
              fontSize: this.scale(12) + 'px',
              color: '#51b8f1'
            }
          },
          goodsIntroduceImgs: {
            box: {
              marginTop: this.scale(50) + 'px',
            },
            img: {
              display: 'block',
              width: this.scale(710) + 'px',
              margin: "0 auto",
            }

          }
        },// style end
        goodsParameterShow: false,
        goodsParameterCountOfRow: this.goodsParameterCountOfRow,// 一行显示的参数个数
        goodsParameterNames: [''],
        goodsParameterValues: {
          parameterValue0: '',
          parameterValue1: '',
          parameterValue2: '',
          parameterValue3: '',
          parameterValue4: '连帽',
          parameterValue5: '秋',
          parameterValue6: '休闲',
          parameterValue7: '简约',
          parameterValue8: 'null',
          parameterValue9: 'null',
          parameterValue10: 'null',
          parameterValue11: 'null',
          parameterValue12: 'null',
          parameterValue13: 'null',
          parameterValue14: 'null'
        },
        goodsIntroduceImgs:
          [
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_0_750_500&imageView&thumbnail=710x0&quality=95',
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_500_750_500&imageView&thumbnail=710x0&quality=95',
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_1000_750_500&imageView&thumbnail=710x0&quality=95',
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_1500_750_500&imageView&thumbnail=710x0&quality=95',
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_2000_750_500&imageView&thumbnail=710x0&quality=95',
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_3000_750_500&imageView&thumbnail=710x0&quality=95',
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_3500_750_500&imageView&thumbnail=710x0&quality=95',
            'https://haitao.nos.netease.com/1925dce7f1a24a459e12ebef49640f9e1535359018421jlc148ms11171.jpg?imageView&quality=98&crop=0_4000_750_500&imageView&thumbnail=710x0&quality=95'
          ]
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      showParameterBoxClick() {
        this.goodsParameterShow = !this.goodsParameterShow;
      },

      getGoodsParameterNames() {
        this.$page.get("/goods/getGoodsParameterName", {goodsParentId: this.goodsParentId}, null,
          response => {
            this.goodsParameterNames = response.data;
          })
      },

      getGoodsParameterValues() {
        this.$page.get("/goods/getGoodsParameterValue", {goodsParentId: this.goodsParentId}, null,
          response => {
            this.goodsParameterValues = response.data;
          })
      },

      getGoodsIntroduceImg() {
        this.$page.get("/goods/getGoodsIntroduceImg", {goodsParentId: this.goodsParentId}, null,
          response => {
            this.goodsIntroduceImgs = response.data;
          })
      }

    },
    beforeCreate() {
      this.goodsParameterCountOfRow = 3;// 一行显示3个商品参数
    },

    mounted() {
      this.getGoodsParameterNames();
      this.getGoodsParameterValues();
      this.getGoodsIntroduceImg();
    }
  }

</script>

<style scoped>

  dl, dt, dd, ul, ol, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
</style>



