<template>
  <footer>
    <ul :style="style.footer.ul">
      <li :style="style.footer.li">
        <div :style="style.footer.label">正</div>
        <div style="float: left">
          <strong>100%正品</strong>
        </div>
        <div style="float: left">
          <span>正品保障 假一赔十</span>
        </div>
      </li>
      <li :style="style.footer.li">
        <div :style="style.footer.label">低</div>
        <div style="float: left">
          <strong>低价保障</strong>
        </div>
        <div style="float: left">
          <span>缩减中间环节 确保低价</span>
        </div>
      </li>
    </ul>
  </footer>
</template>

<script>
  export default {
    name: "homeFooter",
    data() {
      return {
        style: {
          footer: {
            ul: {
              margin: this.scale(100) + 'px',
              display: 'flex',
              justifyContent: 'space-around'
            },
            li: {
              display: 'inline-block',
              width: this.scale(232) + 'px',
            },
            label: {
              width: this.scale(44) + 'px',
              height: this.scale(44) + 'px',
              borderRadius: '50%',
              border: this.scale(2) + 'px ' + 'solid #d6d6d6',
              textAlign: 'center',
              lineHeight: this.scale(44) + 'px',
              fontSize: this.scale(20) + 'px',
              fontWeight: 700,
              display: 'inline-block',
              float: 'left',
              marginRight: this.scale(12) + 'px',
            }
          }
        }
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
    },
  }
</script>

<style scoped>

</style>



