<template>
  <div>
  </div>
</template>

<script>
  export default {
    name: "test",
    computed: {
      shrinkImg() {// 缩小的图片
        return document.querySelector("#shrinkImg");
      },
      shrinkImgX() {// 缩小的图片距离浏览器客户端的水平距离
        return this.shrinkImg.getBoundingClientRect().x;
      },
      shrinkImgY() {// 缩小的图片距离浏览器客户端的垂直距离
        return this.shrinkImg.getBoundingClientRect().y;
      },
      shrinkImgWidth() {// 缩小的图片的宽度
        return this.shrinkImg.style.width.substr(0, this.shrinkImg.style.width.length - 2);
      },
      shrinkImgHeight() {// 缩小的图片的高度
        return this.shrinkImg.style.height.substr(0, this.shrinkImg.style.height.length - 2);
      },
      magnifier() {// 放大镜
        return document.querySelector("#magnifier");
      },
      magnifyImg() {// 放大的图片
        return document.querySelector("#magnifyImg");
      },
      username: {
        get() {
          return this.$store.state.userInfo.user.username;
        },
        set(value) {
          this.$store.commit("userInfo/updateUsername", value);
        }
      }
    },
    methods: {
      shrinkImgMove(event) {
        this.magnifier.style.visibility = 'visible';
        this.magnifyImg.style.visibility = 'visible';
        let effectiveMovement = 0;
        if ((event.clientX - this.shrinkImgX) <= this.shrinkImgWidth / 4) {
          this.magnifyImg.style.left = '0px';
          this.magnifier.style.left = '0px';
        } else if ((event.clientX - this.shrinkImgX) > this.shrinkImgWidth / 4 && (event.clientX - this.shrinkImgX) < this.shrinkImgWidth * 3 / 4) {
          effectiveMovement = event.clientX - this.shrinkImgX - this.shrinkImgWidth / 4;
          this.magnifyImg.style.left = effectiveMovement * -2 + 'px';
          this.magnifier.style.left = effectiveMovement + 'px';
        } else {
          this.magnifyImg.style.left = -this.shrinkImgWidth + 'px';
          this.magnifier.style.left = this.shrinkImgWidth / 2 + 'px';
        }

        if ((event.clientY - this.shrinkImgY) <= this.shrinkImgHeight / 4) {
          this.magnifyImg.style.top = '0px';
          this.magnifier.style.top = '0px';
        } else if ((event.clientY - this.shrinkImgY) > this.shrinkImgHeight / 4 && (event.clientY - this.shrinkImgY) < this.shrinkImgHeight * 3 / 4) {
          effectiveMovement = event.clientY - this.shrinkImgY - this.shrinkImgHeight / 4;
          this.magnifyImg.style.top = effectiveMovement * -2 + 'px';
          this.magnifier.style.top = effectiveMovement + 'px';
        } else {
          this.magnifyImg.style.top = -this.shrinkImgHeight + 'px';
          this.magnifier.style.top = this.shrinkImgHeight / 2 + 'px';
        }
      },
      shrinkImgLeave() {
        this.magnifier.style.visibility = 'hidden';
        this.magnifyImg.style.visibility = 'hidden';
      }
    },

    mounted() {

    }

  }
</script>

<style scoped>
  #magnifier {
    background-image: url(../../assets/test/point.png);
    cursor: move;
    position: absolute;
    width: 200px;
    height: 200px;
    z-index: 1;
    background-repeat: repeat
  }
</style>



