<template>
  <div>
    <!--下拉菜单-->
    <ul id="sideNav" :style="style.sideNav">
      <li :style="[style.sideNavLiMore]"
          @mousemove="isSideNavMenuOnHover(true)"
          @mouseleave="isSideNavMenuOnHover(false)">
            <span :style="[style.sideNavSpanMore]">
                          所有分类
            </span>
      </li>
      <!--侧边导航详情-->
      <li :style="style.sideNavDetail"
          @mousemove="isSideNavMenuOptionDetailOnHover(true)"
          @mouseleave="isSideNavMenuOptionDetailOnHover(false)">
        <!--详情部分-->
        <template v-for="(goodsL1Category) in goodsCategories[userSelectedGoodsCategories[0].l0].goodsL1Categories">
          <dl :style="style.sideNavDetailItem">
            <!--详情部分的title-->
            <dt>
              <div :style="style.sideNavDetailItemTitle.box">
                <!--暂时不能使用二级类别进行搜索，等以后再加上去-->
                <!--<a :style="style.sideNavDetailItemTitle.link"
                   :href="goodsL1Category.id">
                  {{goodsL1Category.value}}
                </a>-->
                <span :style="style.sideNavDetailItemTitle.link"
                      :href="goodsL1Category.id">
                  {{goodsL1Category.value}}
                </span>
              </div>
            </dt>
            <!--详情部分的content-->
            <dd>
              <div :style="style.sideNavDetailItemContent.box">
                <template v-for="goodsL2Category in goodsL1Category.goodsL2Categories">
                  <!--如果是热销类别,就加不同的样式-->
                  <template v-if="goodsL2Category.isHot === 1">
                    <a :style="style.sideNavDetailItemContent.hotlink"
                       @click="categoryClick(goodsL2Category.id,goodsL2Category.value)">
                      {{goodsL2Category.value}}
                    </a>
                  </template>
                  <template v-else>
                    <a :style="style.sideNavDetailItemContent.link"
                       @click="categoryClick(goodsL2Category.id,goodsL2Category.value)">
                      {{goodsL2Category.value}}
                    </a>
                  </template>
                </template>
              </div>
            </dd>
          </dl>
        </template>
      </li>
      <!--侧边导航选项-->
      <li id="sideNavOptions"
          @mousemove="isSideNavMenuOptionOnHover(true)"
          @mouseleave="isSideNavMenuOptionOnHover(false)"
          :style="style.sideNavPullDown">
        <ul>
          <template v-for="(goodsL0Category,goodsL0CategoryIndex) in goodsCategories">
            <li :style="[style.sideNavPullDownBox]" class="sideNavLi"
                @mousemove="setSideNavDetail(goodsL0CategoryIndex)">
              <div>
                 <span :style="style.sideNavPullDownLeft">
                   <img :src="goodsL0Category.iconSrc" :style="style.sideNavPullDownIcon"/>
                   <span :style="style.sideNavPullDownText">{{goodsL0Category.value}}</span>
                 </span>
                <img src="../../assets/home/nav/redRightArrow.png" :style="style.sideNavPullDownIcon"/>
              </div>
            </li>
          </template>
        </ul>
      </li>
    </ul>
    <!--轮播图-->
    <template v-if="showCarousel">
      <div :style="style.carousel.box">
        <el-carousel :interval="2000" :height="style.carousel.height">
          <el-carousel-item v-for="item in style.carousel.imgSrc" :key="item.index">
            <img :style="style.carousel.imgStyle" :src="item.src">
          </el-carousel-item>
        </el-carousel>
      </div>
    </template>
  </div>
</template>

<script>
  export default {
    props: {
      // 是否显示轮播组件
      showCarousel: {
        type: Boolean,
        default: false,
      }
    },
    data() {
      return {
        style: {
          sideNav: {
            position: 'relative',
            left: this.scale(70) + 'px',
            width: this.scale(1100) + 'px',
            zIndex: 999,
          },
          // '所有分类'对应的<li>元素的样式
          sideNavLiMore: {
            color: '#FFF',
            background: 'linear-gradient(to right, #f01423, #EE2C2C,#f01468)',
            width: this.scale(160) + 'px',
            borderLeft: this.scale(1) + 'px solid #ff4d51',
            borderBottom: this.scale(1) + 'px solid #ff4d51',
            borderRight: this.scale(1) + 'px solid #ff4d51',
            cursor: 'pointer',
            lineHeight: this.scale(45) + 'px',
          },
          // '所有分类'对应的<span>元素的样式
          sideNavSpanMore: {
            display: 'inline-block',
            marginLeft: this.scale(10) + 'px',
            width: this.scale(160) + 'px',
            lineHeight: this.scale(45) + "px",
            textIndent: this.scale(43) + 'px',//首行缩进，用来设置文字和图标的间隔
            background: "url(" + require("../../assets/home/nav/more.png") + ")",
            backgroundRepeat: 'no-repeat',
            backgroundPosition: '0 center',
          },

          sideNavPullDown: {
            position: 'absolute',
          },

          sideNavOnHover: false,// 侧边栏'所有分类'处于hover状态。
          sideNavPullDownOnHover: false,// 侧边栏下拉菜单处于hover状态
          sideNavPullDownDetailOnHover: false,// 侧边栏下拉菜单详情处于hover状态

          // '所有分类'下面的选项的<li>元素的公共样式
          sideNavPullDownBox: {
            display: 'none',//初始时隐藏'所有分类'下面的选项
            width: this.scale(150) + 'px',
            borderLeft: this.scale(1) + 'px solid #ff4d51',
            borderBottom: this.scale(1) + 'px solid #ff4d51',
            borderRight: this.scale(1) + 'px solid #ff4d51',
            paddingLeft: this.scale(10) + 'px',
            fontSize: this.scale(14) + 'px',
            cursor: 'pointer',
            height: this.scale(45) + 'px',
            lineHeight: this.scale(45) + 'px',
          },

          // '所有分类'下面的选项的<span>元素中图片的样式，使图片和文字处于同一水平线
          sideNavPullDownIcon: {
            verticalAlign: 'middle',
            width: this.scale(22) + 'px',//必须和图片的尺寸保持一致
            height: this.scale(22) + 'px',//必须和图片的尺寸保持一致
          },
          sideNavPullDownLeft: {
            width: this.scale(105) + 'px',
            display: 'inline-block',
            verticalAlign: 'middle',
          },
          // '所有分类'下面的选项的<span>元素中文字的样式
          sideNavPullDownText: {
            verticalAlign: 'middle',
            marginLeft: this.scale(10) + 'px',
            marginRight: this.scale(10) + 'px',
          },

          // 侧边导航栏的详细面板
          sideNavDetail: {
            visibility: 'hidden',//初始时隐藏'所有分类'下面的选项
            width: this.scale(700) + 'px',
            background: '#fff',
            fontSize: 0,
            borderTop: this.scale(1) + 'px solid #ff4d51',
            borderBottom: this.scale(1) + 'px solid #ff4d51',
            borderRight: this.scale(1) + 'px solid #ff4d51',
            position: 'absolute',
            left: this.scale(162) + 'px',
          },

          sideNavDetailItem: {
            display: 'inline-block',
            width: this.scale(700 / 2) + 'px',
          },
          sideNavDetailItemTitle: {
            box: {
              paddingLeft: this.scale(15) + 'px',
              fontSize: this.scale(13) + 'px',
              fontWeight: 'bold',
              height: this.scale(40) + 'px',
              borderBottom: this.scale(1) + 'px solid #cecfd0',
              lineHeight: this.scale(40) + 'px',
            },
            link: {
              color: '#011',
            }
          },
          sideNavDetailItemContent: {
            box: {
              paddingTop: this.scale(20) + 'px',
              fontSize: this.scale(13) + 'px',
              paddingLeft: this.scale(15) + 'px',
              display: 'flex',
              flexDirection: 'row',
              flexWrap: 'wrap',
            },
            link: {
              marginRight: this.scale(30) + 'px',
              marginBottom: this.scale(20) + 'px',
              color: '#666',
              cursor: 'pointer',
            },
            hotlink: {
              marginRight: this.scale(30) + 'px',
              marginBottom: this.scale(20) + 'px',
              color: '#ff1e32',
              cursor: 'pointer',
            }
          },

          // el轮播组件样式
          carousel: {
            box: {
              // 使侧边导航栏显示在上面
              zIndex: 0,
              position: 'relative'
            },
            imgStyle: {
              width: this.$store.state.app.visibleWidth + 'px',
              height: this.scale(400) + 'px',
            },
            height: this.scale(400) + 'px',
            imgSrc: [{
              index: 0,
              src: require("../../assets/home/carousel/carousel0.jpg"),
            }, {
              index: 1,
              src: require("../../assets/home/carousel/carousel1.jpg"),
            }, {
              index: 2,
              src: require("../../assets/home/carousel/carousel2.jpg"),
            }, {
              index: 3,
              src: require("../../assets/home/carousel/carousel3.jpg"),
            }, {
              index: 4,
              src: require("../../assets/home/carousel/carousel4.jpg"),
            }],
          },
        }, // style结束
        goodsCategories: [0], // 后端获取,先用[0]占位
        userSelectedGoodsCategories: [
          {l0: 0},
          {l1: 0},
          {l2: 0},
        ],// 用户当前选择的类别。
        gettingGoodsCategories: false,// 正在获取数据
      }
    },
    methods: {

      scale(value) {
        return this.$root.scale(value);
      },
      // 鼠标是否在导航菜单上的处理
      isSideNavMenuOnHover(isHover) {
        if (isHover == true) {// 鼠标进入'所有分类'
          this.style.sideNavOnHover = true;
          this.style.sideNavPullDownBox.display = 'block';//使导航菜单项可见

          if (this.goodsCategories[0] == 0) {// mounted时数据没获取到
            if (!this.gettingGoodsCategories) {// 保证每次进入时只获取一次goodsCategories
              this.getGoodsCategories();
              this.gettingGoodsCategories = true;
            }
          }
        } else {// 鼠标离开'所有分类'
          setTimeout(function () {
            if (this.style.sideNavPullDownOnHover == false) {// 延迟一ms再判断是否也离开了导航菜单选项
              this.style.sideNavPullDownBox.display = 'none'
              this.style.sideNavOnHover = false;
            }
          }.bind(this), 1)
          if (this.gettingGoodsCategories) {
            this.gettingGoodsCategories = false;
          }
        }
      },

      // 鼠标是否在导航菜单的选项上的处理
      isSideNavMenuOptionOnHover(isHover) {
        if (isHover == true) {// 鼠标进入导航菜单选项
          this.style.sideNavPullDownOnHover = true;
          if (this.style.sideNavPullDownBox.display == 'block') { // 如果此时导航菜单也可见
            this.style.sideNavDetail.visibility = 'visible';// 那么菜单项详情也应该可见
          }
        } else {// 鼠标离开导航菜单选项
          setTimeout(function () {
            if (this.style.sideNavPullDownDetailOnHover == false) {// 延迟一ms再判断是否也离开了菜单项详情
              this.style.sideNavPullDownBox.display = 'none'
              this.style.sideNavPullDownOnHover = false;
              this.style.sideNavOnHover = false;
              this.style.sideNavDetail.visibility = 'hidden';
            } else {
              this.style.sideNavDetail.visibility = 'visible';
            }
          }.bind(this), 1)
        }
      },

      // 鼠标是否在导航菜单项的详情上的处理
      isSideNavMenuOptionDetailOnHover(isHover) {
        if (isHover == true) {
          this.style.sideNavPullDownDetailOnHover = true;
        } else {
          this.style.sideNavPullDownDetailOnHover = false;
          this.style.sideNavDetail.visibility = 'hidden';
          this.style.sideNavPullDownOnHover = false;
          setTimeout(function () {
            if (this.style.sideNavPullDownOnHover == false) {// 延迟1ms再判断鼠标是否没有进入导航菜单选项
              this.style.sideNavPullDownBox.display = 'none';
            }
          }.bind(this), 1)
        }
      },

      // 鼠标在哪一个导航菜单项上，就设置菜单项详情的数据
      setSideNavDetail(index) {
        this.userSelectedGoodsCategories[0].l0 = index;
      },
      /**
       * 处理类别被点击
       * 只能按搜索值或者按类别搜索，不能既按搜索值又联合类别进行搜索
       * @param categoryId
       */
      categoryClick(currentCategoryId, categoryValue) {
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues",
          [
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
          ]
        );
        this.$store.commit("goodsShow/updateCurrentPage", 1);// 查询第一页
        this.$store.commit("goodsShow/updateSearchValue", "");// 点击下拉菜单导航时，需要清除搜索值
        this.$store.commit("goodsShow/updateMinPrice", 0);// 初始化查询条件
        this.$store.commit("goodsShow/updateMaxPrice", 999999999);// 初始化查询条件
        //this.$store.commit("goodsShow/updateGoodsCategoryValue", categoryValue);
        document.location.hash = "/";// 先随便修改地址栏为一个值，这样在同一个路由上只变化query地址栏也会更新。
        let query = {categoryId: currentCategoryId};
        this.$router.push({name: 'goodsDetailCategory', query: query})
      },
      setSideNavOptions() {
        let sideNavOptions = document.querySelector('#sideNavOptions');
        let sideNavOptionsHeight = sideNavOptions.offsetHeight - 2 + 'px';// 减去上下边框的大小
        this.style.sideNavDetail.height = sideNavOptionsHeight;
      },
      getGoodsCategories() {
        this.$page.get('/goods/goodsCategory', null, null,
          response => {
            this.goodsCategories = response.data;
          }
        );
      }
    },

    beforeUpdate() {
      this.setSideNavOptions();
    },
    mounted() {
      this.getGoodsCategories();
    },
  }
</script>

<style scoped>
  ul {
    list-style-type: none;
  }

  dl, dd, dt, li, ul {
    padding: 0px;
    margin: 0px;
  }

  /*侧边导航栏的<li>没有hover时的背景色和字体色*/
  .sideNavLi {
    background: linear-gradient(to right, #f01423, #EE2C2C, #f01468);
    color: white;
  }

  /*侧边导航栏的<li>hover时的背景色和字体色*/
  .sideNavLi:hover {
    background: linear-gradient(to right, #EE2C2C, #ffffff, #ffffff, #ffffff);
    color: #f01423;
  }

  /*取消下拉选项详情中的a标签的下划线*/
  #sideNav li dl a {
    text-decoration: none;
  }

  /*下拉选项详情中的a标签hover时才显示下划线*/
  #sideNav li dl a:hover {
    text-decoration: underline;
  }
</style>



