const state = {
  visibleWidth: window.screen.width * window.devicePixelRatio,// 显示区域的宽度:屏幕物理宽度 - 滚动条宽度
  scrollHeight: document.documentElement.scrollHeight,// 浏览器屏幕可滚动的高度
}

const getters = {
  visibleWidth() {
    return state.visibleWidth;
  },
  scrollHeight() {
    return state.scrollHeight;
  },
}

const mutations = {
  updateVisibleWidth(state, value) {
    state.visibleWidth = value;
  },
  updateScrollHeight(state, value) {
    state.scrollHeight = value;
  }
}

const actions = {
  updateVisibleWidth({commit}, value) {
    commit('updateVisibleWidth', value);
  },
  updateScrollHeight({commit}, value) {
    commit('updateScrollHeight', value);
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



