<template>
  <div>
    <!--header-->
    <hx-home-header></hx-home-header>

    <!--显示用户选择一个类别后的匹配的所有商品-->
    <dl :style="style.container">
      <!--用户已选择的类别-->
      <div :style="style.selected.box">
        <a class="text_hover" :style="style.selected.text"
           @click="selectAllGoodsParent">
          全部结果
        </a>
        <template v-if="this.$store.state.goodsShow.navValue!=''">
          <img :style="style.selected.rightArrow" src="../../assets/goods/right_arrow.png">
          <span class="text_hover"
                @click="clickNavi"
                :style="style.selected.text">{{this.$store.state.goodsShow.navValue}}</span>
        </template>
        <!--<template v-else>
          <img :style="style.selected.rightArrow" src="../../assets/goods/right_arrow.png">
          <span class="text_hover"
                @click="clickNavi('searchValue')"
                :style="style.selected.text">{{this.$store.state.goodsShow.searchValue}}</span>
        </template>-->
        <!--显示已选择的类别-->
        <span :style="style.selected.category.box">
          <ul>
            <template
              v-for="(userSelectedGoodsDetailCategory,userSelectedGoodsDetailCategoryIndex) in this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues"
              v-if="userSelectedGoodsDetailCategory.text!=null">
              <li id="category_box"
                  @click="deleteSelectedCategory(userSelectedGoodsDetailCategoryIndex)"
                  :style="style.selected.category.label.box"
                  title="删除">
                  <span>
                    {{goodsDetailCategoryNames[userSelectedGoodsDetailCategoryIndex].detailCategoryName}}:
                  </span>
                  <span>
                    <!--{{replaceGoodsDetailCategoryValue(userSelectedGoodsDetailCategory)}}&nbsp-->
                    {{userSelectedGoodsDetailCategory.text}}&nbsp
                  </span>
                  <span>
                    ✖
                  </span>
              </li>
            </template>
          </ul>
        </span>
      </div>
      <!--选择类别框-->
      <dt :style="style.selectDetailCategory.box">
        <ul>
          <!--分类-->
          <template v-if="divide.length > 1">
            <li class="clear_space" :style="style.selectDetailCategory.item">
              <!--显示分类-->
              <span id="divideName"
                    :style="style.selectDetailCategory.divide.box">
                <span :style="style.selectDetailCategory.divide.text">分类</span>
                <span :style="style.selectDetailCategory.divide.triangle"></span>
              </span>
              <!--显示分类中的类别值-->
              <ul id="divideValues"
                  :style="style.selectDetailCategory.goodsDetailCategoriesValue.box">
                <template
                  v-for="(divideItem,itemIndex) in divide"
                  v-if="itemIndex < maxRowCategoryValueCount*2">
                  <li :style="style.selectDetailCategory.goodsDetailCategoriesValue.item">
                    <span class="goodsDetailCategoriesValue"
                          @click="divideItemClick($event,divideItem)">
                      {{replaceCategoryIdToValue(divideItem)}}
                    </span>
                  </li>
                </template>
                <template v-else>
                  <template v-if="showDivideMore">
                    <li :style="style.selectDetailCategory.goodsDetailCategoriesValue.item">
                    <span class="goodsDetailCategoriesValue"
                          @click="divideItemClick($event,divideItem)">
                      {{replaceCategoryIdToValue(divideItem)}}
                    </span>
                    </li>
                  </template>
                </template>
                <!--当类别值的个数超过预定值时显示展开,收起按钮-->
                <template
                  v-if="divide.length > maxRowCategoryValueCount*2">
                  <div :style="style.selectDetailCategory.goodsDetailCategoriesValue.showMoreCategoryClickBox">
                    <span v-if="!showDivideMore"
                          style="cursor: pointer"
                          @click="showDivideMore=true">
                      展开&nbsp&nbsp<i class="el-icon-arrow-down"></i>
                    </span>
                    <span v-else
                          style="cursor: pointer"
                          @click="showDivideMore=false">
                      收起&nbsp&nbsp<i class="el-icon-arrow-up"></i>
                    </span>
                  </div>
                </template>
              </ul>
            </li>
          </template>
          <!--商品的详细分类-->
          <template
            v-for="(goodsDetailCategoryName,rowIndex) in goodsDetailCategoryNames"
            v-if="goodsDetailCategoryValues.length>0 && goodsDetailCategoryValues[rowIndex].length > 0">
            <li class="clear_space" :style="style.selectDetailCategory.item">
              <!--显示类别名-->
              <span :id="'goodsDetailCategoryName'+rowIndex"
                    :style="style.selectDetailCategory.goodsDetailCategoryName">
                {{goodsDetailCategoryName.detailCategoryName}}
              </span>
              <!--显示类别名对应的类别值-->
              <ul :id="'goodsDetailCategoriesValueBox'+rowIndex"
                  :style="style.selectDetailCategory.goodsDetailCategoriesValue.box">
                <template v-for="(goodsDetailCategoriesValue,colIndex) in goodsDetailCategoryValues[rowIndex]">
                  <template v-if="colIndex < maxRowCategoryValueCount*goodsDetailCategoryName.maxShowRow">
                    <li :style="style.selectDetailCategory.goodsDetailCategoriesValue.item">
                      <span class="goodsDetailCategoriesValue"
                            @click="goodsDetailCategoriesValueClick(rowIndex,goodsDetailCategoriesValue)">
                        {{goodsDetailCategoriesValue.text}}
                      </span>
                    </li>
                  </template>
                  <template v-else>
                    <template v-if="goodsDetailCategoryName.show">
                      <li :style="style.selectDetailCategory.goodsDetailCategoriesValue.item">
                        <span class="goodsDetailCategoriesValue"
                              @click="goodsDetailCategoriesValueClick(rowIndex,goodsDetailCategoriesValue)">
                          {{goodsDetailCategoriesValue.text}}
                        </span>
                      </li>
                    </template>
                  </template>
                </template>
                <!--当类别值的个数超过预定值时显示展开,收起按钮-->
                <template
                  v-if="goodsDetailCategoryValues[rowIndex].length > maxRowCategoryValueCount*goodsDetailCategoryName.maxShowRow">
                  <div :style="style.selectDetailCategory.goodsDetailCategoriesValue.showMoreCategoryClickBox">
                      <span v-if="!goodsDetailCategoryName.show"
                            style="cursor: pointer"
                            @click="showMoreCategoryClick(rowIndex)">
                        展开&nbsp&nbsp<i class="el-icon-arrow-down"></i>
                      </span>
                    <span v-else
                          style="cursor: pointer"
                          @click="showMoreCategoryClick(rowIndex)">
                        收起&nbsp&nbsp<i class="el-icon-arrow-up"></i>
                      </span>
                  </div>
                </template>
              </ul>
            </li>
          </template>
        </ul>
      </dt>
      <!--展示匹配的商品-->
      <dd :style="style.selectDetailCategory.box">
        <hx-goods-show :pagi-goods-parent="pagiGoodsParent"/>
      </dd>
    </dl>
  </div>
</template>

<script>
  import homeHeader from '../home/homeHeader'
  import goodsShow from '../goods/goodsShow'

  export default {
    name: "goodsDetailCategory",
    components: {
      'hx-home-header': homeHeader,
      'hx-goods-show': goodsShow,
    },

    data() {
      return {
        style: {
          container: {
            minHeight: this.scale(window.innerHeight) + 'px',
            backgroundColor: '#e9ebec',
          },
          selected: {
            box: {
              padding: this.scale(15) + 'px 0',
              width: this.scale(1205) + 'px',
              fontSize: this.scale(13) + 'px',
              color: '#333',
              margin: '0 auto',
            },
            text: {
              verticalAlign: 'middle',
            },
            rightArrow: {
              verticalAlign: 'middle',
              width: this.scale(16) + 'px',
              height: this.scale(16) + 'px',
              marginRight: this.scale(4) + 'px'
            },
            category: {
              box: {
                verticalAlign: 'top',
                display: 'inline-block',
                width: this.scale(800) + 'px',
                marginLeft: this.scale(10) + 'px',
                minHeight: this.scale(30) + 'px',
              },
              label: {
                box: {
                  display: 'inline-block',
                  fontSize: this.scale(12) + 'px',
                  marginRight: this.scale(10) + 'px',
                  border: '1px dotted #e31436',
                  padding: this.scale(3) + 'px ' + this.scale(2) + 'px',
                  color: '#e31436',
                  backgroundColor: 'white',
                  cursor: 'pointer',
                },
              }
            }
          },
          selectDetailCategory: {
            box: {
              width: this.scale(1100) + 'px',
              margin: '0 auto',
            },
            item: {
              marginBottom: '1px',
            },
            goodsDetailCategoryName: {
              display: 'inline-block',
              width: this.scale(85) + 'px',
              paddingLeft: this.scale(15) + 'px',
              paddingTop: this.scale(10) + 'px',
              fontSize: this.scale(12) + 'px',
              verticalAlign: 'middle',
              backgroundColor: '#f8f8f8',
            },
            divide: {
              box: {
                display: 'inline-block',
                width: this.scale(85) + 'px',
                paddingLeft: this.scale(15) + 'px',
                paddingTop: this.scale(10) + 'px',
                fontSize: this.scale(12) + 'px',
                verticalAlign: 'middle',
                backgroundColor: '#f8f8f8',
              },
              text: {
                verticalAlign: 'middle',
              },
              triangle: {
                display: 'inline-block',
                backgroundImage: "url(" + require("../../assets/goods/goodsDetailCategory/right_triangle.png") + ")",
                backgroundSize: this.scale(10) + 'px ' + this.scale(10) + 'px',
                backgroundRepeat: 'no-repeat',
                height: this.scale(10) + 'px',
                width: this.scale(10) + 'px',
                verticalAlign: 'middle',
              }
            },
            goodsDetailCategoriesValue: {
              box: {
                display: 'inline-block',
                paddingLeft: this.scale(15) + 'px',
                paddingTop: this.scale(10) + 'px',
                minHeight: this.scale(25) + 'px',
                backgroundColor: 'white',
                width: this.scale(1100 - 85 - 15 - 15) + 'px',
                verticalAlign: 'top',
              },
              item: {
                display: 'inline-block',
                marginRight: this.scale(10) + 'px',
                marginBottom: this.scale(10) + 'px',
                verticalAlign: 'top',
                width: this.scale((1100 - 10 * this.maxRowCategoryValueCount - 85 - 15 - 120) / this.maxRowCategoryValueCount) + 'px',// 使一行最多显示this.maxRowCategoryValueCount个类别值
                fontSize: this.scale(12) + 'px',
              },
              showMoreCategoryClickBox: {
                float: 'right',
                display: 'inline-block',
                marginLeft: this.scale(10) + 'px',
                width: this.scale(90) + 'px',
                height: this.scale(20) + 'px',
                fontSize: this.scale(12) + 'px',
                color: '#51b8f1'
              }
            }
          }
        },// style end
        //currentCategoryId: 0,// 保存当前用户选择的商品类别
        //searchValue: '',
        userSelectedGoodsDetailCategories: [],// 第一次由后端查数据库生成
        goodsDetailCategoryValuesDic: [],// 是数据字典,用来将数据库中存储的goods_detail_value数字值替换成真实值。
        goodsDetailCategoryNames: [],
        goodsDetailCategoryValues: [],// 由goods生成
        goodsParent: [],
        pagiGoodsParent: [],// 分页后的goodsParent;因为需要生成详细类别值，因此后端要查出所有商品，然后前端进行分页。
        maxRowCategoryValueCount: this.maxRowCategoryValueCount,// 一行最多显示多少个类别值
        divide: [],// 保存分类值
        goodsL2CategoryDic: [],// 用来将divide中的category_id替换成文字
        showDivideMore: false,// 分类值超过预设个数时，当前是否是显示更多分类值的状态,
        showDivideMore: false,// 分类值超过预设个数时，当前是否是显示更多分类值的状态,
        //pageSize: 2,// 每一页的个数
        //itemCount: 0,// 查询到的商品总数
        //currentPage: 1// 当前显示的页
      }
    },

    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      /**
       * 查询所有商品
       *
       */
      selectAllGoodsParent() {
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues",
          [
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
          ]
        );
        this.$store.commit("goodsShow/updateNavValue", "")
        this.$store.commit("goodsShow/updateCurrentCategoryId", null);
        //this.$store.commit("goodsShow/updateGoodsCategoryValue", "");
        this.$store.commit("goodsShow/updateSearchValue", "");
        //this.$store.commit("goodsShow/updateMinPrice", 0);
        //this.$store.commit("goodsShow/updateMaxPrice", 999999999);
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        //this.getGoodsParentAndUserSelectedGoodsDetailCategory();
        document.location.hash = "/";// 先随便修改地址栏为一个值，这样在同一个路由上只变化query地址栏也会更新。
        this.$router.push({name: 'goodsDetailCategory'})
      },

      clickNavi() {
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues",
          [
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
          ]
        );
        this.getGoodsParentAndUserSelectedGoodsDetailCategory();
      },

      deleteSelectedCategory(userSelectedGoodsDetailCategoryIndex) {
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        let userSelectedGoodsDetailCategoryValues = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues));
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].text = null;
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].id = null;
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].categoryValueOrder = null;
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].fieldIndex = null;
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues", userSelectedGoodsDetailCategoryValues);
        this.getGoodsParentAndUserSelectedGoodsDetailCategory()

      },

      /**
       * 点击详细分类
       */
      goodsDetailCategoriesValueClick(rowIndex, goodsDetailCategoriesValue) {
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        let userSelectedGoodsDetailCategoryValues = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues));
        userSelectedGoodsDetailCategoryValues[rowIndex] = goodsDetailCategoriesValue;
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues", userSelectedGoodsDetailCategoryValues);
        this.getGoodsParentAndUserSelectedGoodsDetailCategory();
      },

      /**
       * 处理点击分类值
       */
      divideItemClick(event, categoryId) {
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues",
          [
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
          ]
        );
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        this.$store.commit("goodsShow/updateCurrentCategoryId", categoryId);
        this.getGoodsCategoryValue();
        /* if (this.$store.state.goodsShow.currentCategoryId != null) {
           this.$store.commit("goodsShow/updateNavValue", this.$store.state.goodsShow.goodsCategoryValue)
         } else {
           this.$store.commit("goodsShow/updateNavValue", this.$store.state.goodsShow.searchValue)
         }*/
        this.getGoodsParentAndUserSelectedGoodsDetailCategory();
        let goodsDetailCategoriesValue = document.querySelectorAll(".goodsDetailCategoriesValue");
        goodsDetailCategoriesValue.forEach(value => {
          value.style.color = "#000";
        })
        event.currentTarget.style.color = "red"
      },

      /**
       * 进入goodsDetailCategory的时候如果存在currentCategoryId，则获取对应的goodsCategoryValue
       */
      getGoodsCategoryValue() {
        if (this.$store.state.goodsShow.currentCategoryId != null) {
          this.$page.get('goods/goodsCategoryValue', {id: this.$store.state.goodsShow.currentCategoryId}, null,
            response => {
              //this.$store.commit("goodsShow/updateGoodsCategoryValue", response.data.goodsCategoryValue);
              this.$store.commit("goodsShow/updateNavValue", response.data.goodsCategoryValue)
              return;
            },
            /* error => {
               this.userSelectedGoodsDetailCategories = null;
             }*/
          )
        }
        this.$store.commit("goodsShow/updateNavValue", this.$store.state.goodsShow.searchValue)
      },

      /*getGoodsParent() {
        let params = {
          categoryId: this.currentCategoryId,
          searchValue: this.searchValue,
          orderBy: 'category_id',
          pageNum: 0,
          pageCount: 1,
        };
        this.$page.get('goods/goodsParent', params,
          response => {
            this.goodsParent = response.data;
          },
          error => {
            this.goodsParent = null;
            this.userSelectedGoodsDetailCategories = null;
          })
      },*/

      /**
       * 生成分类值
       */
      generateDivide() {
        this.divide = [];// 初始化分类值
        this.goodsParent.forEach((value, index) => {
          if (this.divide.every(categoryId => categoryId != value.categoryId)) {// divide中不包含被遍历的value.categoryId
            this.divide.push(value.categoryId);// 就保存
          }
        })
        //this.divide.sort((a, b) => a - b);
        //if (this.divide.length > 1) {
        if (this.divide.length > 1) {
          if (this.goodsL2CategoryDic.length == 0) {// 如果之前已经请求过就不需要再请求该数据
            this.$page.get("/goods/goodsL2Category", null, null,
              response => {
                this.goodsL2CategoryDic = response.data;
                this.divide.sort((a, b) => a - b)
              })
          }
          let goodsDetailCategoriesValue = document.querySelectorAll(".goodsDetailCategoriesValue");
          goodsDetailCategoriesValue.forEach(value => {
            value.style.color = "#000";
          })
        }else {
          this.divide = [];// 如果只有一个分类或没有分类，就不显示分类
        }
      },

      handleCategoryIsNotNull(response) {
        // 因为有可能返回的是多个类别下的商品，那么就取第一个商品的类别的详细类别作为显示的类别，其他类别通过分类来导航
        this.getGoodsDetailCategoryName()
      },

      /**
       * 类别为空，也就是按搜索值查询商品。因此需要处理可能商品存在于多个分类的情况
       * 暂时选取第一个商品显示它的详细类别。其他类别用分类表示。
       */
      handleCategoryIsNull(response) {
        //this.$store.commit("goodsShow/updateCurrentCategoryId", this.goodsParent[0].categoryId)
        //this.currentCategoryId = this.goodsParent[0].categoryId;
        // 因为有可能返回的是多个类别下的商品，那么就取第一个商品的类别的详细类别作为显示的类别，其他类别通过分类来导航
        this.getGoodsDetailCategoryName()
      },

      /**
       * 根据查询条件获取goodsParent
       *
       */
      getGoodsParentAndUserSelectedGoodsDetailCategory() {
        let params = {
          categoryId: this.$store.state.goodsShow.currentCategoryId,
          searchValue: this.$store.state.goodsShow.searchValue,
          orderBy: this.$store.state.goodsShow.orderBy,
          currentPage: this.$store.state.goodsShow.currentPage,// 默认获取第一页的数据
          pageSize: this.$store.state.goodsShow.pageSize,
          minPrice: this.$store.state.goodsShow.minPrice,
          maxPrice: this.$store.state.goodsShow.maxPrice,
        };
        let bodyData = this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues;
        /*let bodyData = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues));
        bodyData.forEach((v, i, array) => {
          delete array[i].text;
          delete array[i].categoryValueOrder;
        })*/
        this.$page.post('goods/goodsParent', params, bodyData,
          response => {

            this.goodsParent = response.data.goods_parent_list_key;
            this.$store.commit("goodsShow/updateItemCount", response.data.item_count_key)// 保存不分页时商品的总数目
            let startIndex = (this.$store.state.goodsShow.currentPage - 1) * this.$store.state.goodsShow.pageSize;
            let endIndex = this.$store.state.goodsShow.currentPage * this.$store.state.goodsShow.pageSize;
            this.pagiGoodsParent = this.goodsParent.filter((value, index) => index >= startIndex && index < endIndex)
            this.generateDivide();
            this.getGoodsDetailCategoryName()

            //this.handleCategoryIsNull(response);

           /* if (this.$store.state.goodsShow.currentCategoryId == null) {
              this.handleCategoryIsNull(response);
            } else {
              this.handleCategoryIsNotNull(response);
            }*/
          },
          error => {
            if (error.status === 404) {
              this.$message({
                message: '未查询到任何商品!',
                type: 'warning',
                center: true,//文字居中
              });
            } else if (error.status >= 500) {
              this.$message({
                message: '服务器繁忙,请稍后重试!',
                type: 'warning',
                center: true,//文字居中
              });
            }
            this.$store.commit("goodsShow/updateItemCount", 0)
            this.pagiGoodsParent = [];
            this.goodsParent = [];
            this.goodsDetailCategoryValues = [];
            this.divide = [];
          })
      },

      /**
       * 将goodsParent中的所有detailCategoryValue属性提取出来
       */
      generateGoodsDetailCategoriesValues() {
        this.goodsDetailCategoryValues = [];
        for (let i = 0; i < 6; i++) {
          this.goodsDetailCategoryValues.push([]);
          this.goodsParent.forEach((goodsParentItem, index, array) => {
            this.goodsDetailCategoryValues[i].push({text: null, id: null, categoryValueOrder: null, fieldIndex: null})//text用来显示，id用来传给后端查询，因为后端保存的商品的goodsDetailCategoryValue是id.order用来对每一行中的类别值排序
            // 因为有可能返回的是多个类别下的商品，那么就取第一个商品的类别的详细类别作为显示的类别，其他类别通过分类来导航
            if (goodsParentItem.categoryId == this.goodsParent[0].categoryId) {
              if (goodsParentItem['detailCategoryValue' + i] != null) {// 只判断不为空的detailCategoryValue
                // 如果该行行重复了就不添加
                if (this.goodsDetailCategoryValues[i].every(goodsDetailCategoriesValue => goodsDetailCategoriesValue.id != goodsParentItem['detailCategoryValue' + i])) {
                  let goodsDetailCategoryValue = this.goodsDetailCategoryValuesDic.find(value => value.id == goodsParentItem['detailCategoryValue' + i]);
                  let goodsDetailCategoryName = this.goodsDetailCategoryNames.find(value => value.id == goodsDetailCategoryValue.goodsDetailCategoryId);
                  let detailCategoryNameOrder = goodsDetailCategoryName.detailCategoryOrder;
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].id = goodsParentItem['detailCategoryValue' + i];
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].fieldIndex = i;// 用来传给后端时，后端才知道用户选择的类别值对应哪一个detail_category_value字段
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].text = goodsDetailCategoryValue.categoryValue;//text用来显示，
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].categoryValueOrder = goodsDetailCategoryValue.categoryValueOrder;
                }
              }
            }
          })
          // 因为存在不满足goodsParentItem.categoryId == this.goodsParent[0].categoryId，因此要去掉空detailCategoryValue属性
          this.goodsDetailCategoryValues[i] = this.goodsDetailCategoryValues[i].filter((value, index, array) => {
              return value.text != '' && value.text != null
            }
          )
          // 按数据库中的categoryValueOrder排序
          this.goodsDetailCategoryValues[i].sort((a, b) => a.categoryValueOrder - b.categoryValueOrder)
        }

      },

      /**
       * 获取goods_detail_category_name
       */
      getGoodsDetailCategoryName() {

        // 因为查询的所有商品有可能有多个类别，因此只显示第一个商品的详细类别，其他详细类别显示在分类中。
        let categoryId = this.goodsParent[0].categoryId;
        this.$page.get('goods/goodsDetailCategoryNames', {categoryId: categoryId}, null,
          response => {
            this.goodsDetailCategoryNames = response.data.goods_detail_category_name_list_key;
            //this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategories", response.data.user_selected_goods_detail_category_list_key)
            //this.userSelectedGoodsDetailCategories = response.data.user_selected_goods_detail_category_list_key;
            this.goodsDetailCategoryValuesDic = response.data.goods_detail_category_value_list_key;
            this.goodsDetailCategoryNames.forEach((value, index) => {
              this.$set(value, 'show', false)// 该行的详细类别值的超出部分是否显示。必须使用$set()添加属性，否则双向绑定就失效了。
            })
            //console.log(this.userSelectedGoodsDetailCategories)
            this.generateGoodsDetailCategoriesValues();
          },
          /* error => {
             this.goodsParent = null;
             this.userSelectedGoodsDetailCategories = null;
           }*/
        )
      },

      replaceGoodsDetailCategoryValue(goodsDetailCategoriesValue) {
        for (let i = 0; i < this.goodsDetailCategoryValuesDic.length; i++) {
          if (this.goodsDetailCategoryValuesDic[i].id == goodsDetailCategoriesValue) {
            return this.goodsDetailCategoryValuesDic[i].categoryValue;
          }
        }
      },

      replaceCategoryIdToValue(categoryId) {
        for (let i = 0; i < this.goodsL2CategoryDic.length; i++) {
          if (this.goodsL2CategoryDic[i].id == categoryId) {
            return this.goodsL2CategoryDic[i].value;
          }
        }
      },

      showMoreCategoryClick(rowIndex) {
        /* let userSelectedGoodsDetailCategories = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategories));
         userSelectedGoodsDetailCategories[rowIndex].show = !userSelectedGoodsDetailCategories[rowIndex].show;
         this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategories", userSelectedGoodsDetailCategories)*/
        this.goodsDetailCategoryNames[rowIndex].show = !this.goodsDetailCategoryNames[rowIndex].show;
      },

      setGoodsDetailCategoryNameStyle() {
        // 使分类名与分类值的高度一致。
        let divideName = document.querySelector("#divideName");
        if (divideName != null) {
          let divideValues = document.querySelector("#divideValues");
          divideName.style.height = divideValues.offsetHeight - this.scale(10) + 'px';// 减去paddingTop的值
        }

        // 使goodsDetailCategoriesValueBox与goodsDetailCategoryName的高度一致。
        if (this.goodsDetailCategoryNames != null && this.goodsDetailCategoryNames.length > 0) {
          this.goodsDetailCategoryNames.forEach((value, index) => {
            let goodsDetailCategoriesValueBox = document.querySelector("#goodsDetailCategoriesValueBox" + index);
            if (goodsDetailCategoriesValueBox != null || goodsDetailCategoriesValueBox != undefined) {
              let goodsDetailCategoryName = document.querySelector("#goodsDetailCategoryName" + index);
              goodsDetailCategoryName.style.height = goodsDetailCategoriesValueBox.offsetHeight - this.scale(10) + 'px';// 减去paddingTop的值
            }
          })
        }
      }
    },
    beforeCreate() {
      this.maxRowCategoryValueCount = 5;
    },

    mounted() {
      //this.getGoodsParentAndUserSelectedGoodsDetailCategory(this.$route.query);
    },

    updated() {
      this.setGoodsDetailCategoryNameStyle();
    },

    beforeRouteEnter(to, from, next) {
      next(vm => {
        if (to.query.categoryId != null) {// 如果地址栏有categoryId，那么直接刷新的时候需要保存categoryId
          vm.$store.commit("goodsShow/updateCurrentCategoryId", to.query.categoryId);

        }
        if (to.query.searchValue != null) {// 如果地址栏有searchValue，那么直接刷新的时候需要保存searchValue
          vm.$store.commit("goodsShow/updateSearchValue", to.query.searchValue);
        }
        //vm.currentCategoryId = to.query.categoryId;
        //vm.searchValue = to.query.searchValue;
        vm.getGoodsCategoryValue();
        //vm.getGoodsParentAndUserSelectedGoodsDetailCategory(vm.currentCategoryId, vm.searchValue);
        vm.getGoodsParentAndUserSelectedGoodsDetailCategory();
      })
    },
  }
</script>

<style scoped>
  dl, dt, dd, ul, ol, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .clear_space {
    font-size: 0;
  }

  .text_hover:hover {
    cursor: pointer;
    color: #ff4d51;
    text-decoration: underline;
  }

  #category_box:hover :nth-child(3) {
    background-color: #e31436;
    color: white;
  }

  .goodsDetailCategoriesValue:hover {
    color: #e31436;
    cursor: pointer;
  }
</style>



