<template>
  <div>
    <div :style="style.container">
      <div :style="style.centerContainer">
        <div :style="style.table">
          <div v-for="item in collectTable"
               :style="item.style.goods.grid"
               @mouseover="onCollectGridHover(collectTable.indexOf(item))"
               @mouseleave="onCollectGridUnHover(collectTable.indexOf(item))"
          >
            <!--hover时的每一个格子-->
            <div style="position: relative">
              <div :style="item.style.goods.hoverBox">
                <!--商品图片-->
                <div>
                  <!--商品左上角的标签-->
                  <div v-if="item.data.leftTopLabel" :style="item.style.goods.goodsImg.leftTopLabel">
                    <div>{{item.data.leftTopLabel}}</div>
                  </div>
                  <!--删除商品-->
                  <a href="#" :style="item.style.goods.goodsImg.deleteGoods">
                    <el-tooltip effect="dark" content="删除宝贝" placement="top">
                      <img src="../../assets/user/delete.png">
                    </el-tooltip>
                  </a>
                  <!-- 商品图片 -->
                  <a :href="item.data.href">
                    <img :style="item.style.goods.goodsImg.img" :src="item.data.imgSrc">
                  </a>
                </div>
                <!--商品图片底部的部分-->
                <div>
                  <!--商品标题-->
                  <span class="goods-title" :style="item.style.goods.bottomOfGoodsImg.hoverGoodsTitle">
                    <a href="#">{{item.data.title}}</a>
                  </span>
                  <!--商品格子中部的标签-->
                  <div :style="item.style.goods.bottomOfGoodsImg.middleLabel">
                    {{item.data.middleLabel}}
                  </div>
                  <!--商品底部-->
                  <div :style="item.style.goods.bottomOfGoodsImg.bottom.box">
                    <!--打折价-->
                    <span style="font-size: 0">
                    <span :style="item.style.goods.bottomOfGoodsImg.bottom.moneySymbol">¥</span>
                    <span :style="item.style.goods.bottomOfGoodsImg.bottom.discount">{{item.data.discount}}</span>
                  </span>
                    <!--原价-->
                    <span style="font-size: 0">
                      <del :style="item.style.goods.bottomOfGoodsImg.bottom.originalPrice">¥</del>
                      <del
                        :style="item.style.goods.bottomOfGoodsImg.bottom.originalPrice">{{item.data.originalPrice}}</del>
                    </span>
                    <a class="buy" href="#" :style="item.style.goods.bottomOfGoodsImg.bottom.buy">加入购物车</a>
                  </div>
                </div>
              </div>
            </div>
            <!--未hover时的每一个格子-->
            <div :style="item.style.goods.unHoverBox">
              <!--商品图片-->
              <div>
                <!--商品左上角的标签-->
                <div v-if="item.data.leftTopLabel" :style="item.style.goods.goodsImg.leftTopLabel">
                  <div>{{item.data.leftTopLabel}}</div>
                </div>
                <!-- 商品图片 -->
                <a :href="item.data.href">
                  <img :style="item.style.goods.goodsImg.img" :src="item.data.imgSrc">
                </a>
              </div>
              <!--商品图片底部的部分-->
              <div>
                <!--商品标题-->
                <span class="goods-title" :style="item.style.goods.bottomOfGoodsImg.unHoverGoodsTitle">
                  <a href="#">{{item.data.title}}</a>
                </span>
                <!--商品格子中部的标签-->
                <div :style="item.style.goods.bottomOfGoodsImg.middleLabel">
                  {{item.data.middleLabel}}
                </div>
                <!--商品底部-->
                <div :style="item.style.goods.bottomOfGoodsImg.bottom.box">
                  <!--打折价-->
                  <span style="font-size: 0">
                    <span :style="item.style.goods.bottomOfGoodsImg.bottom.moneySymbol">¥</span>
                    <span :style="item.style.goods.bottomOfGoodsImg.bottom.discount">{{item.data.discount}}</span>
                  </span>
                  <!--原价-->
                  <span style="font-size: 0">
                      <del :style="item.style.goods.bottomOfGoodsImg.bottom.originalPrice">¥</del>
                      <del
                        :style="item.style.goods.bottomOfGoodsImg.bottom.originalPrice">{{item.data.originalPrice}}</del>
                    </span>
                  <a class="buy" href="#" :style="item.style.goods.bottomOfGoodsImg.bottom.buy">加入购物车</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "collect",
    methods: {
      scale(value) {
        return this.$root.scale(value / 1.2);
      },

      onCollectGridHover(index) {
        if (!this.isCollectGridHover) {
          this.isCollectGridHover = true;
          this.collectTable[index].style.goods.unHoverBox.visibility = 'hidden';
          this.collectTable[index].style.goods.hoverBox.visibility = 'visible';
          this.collectTable[index].style.goods.goodsImg.deleteGoods.visibility = 'visible';
        }
      },

      onCollectGridUnHover(index) {
        if (this.isCollectGridHover) {
          this.isCollectGridHover = false;
          this.collectTable[index].style.goods.unHoverBox.visibility = 'visible';
          this.collectTable[index].style.goods.hoverBox.visibility = 'hidden';
          this.collectTable[index].style.goods.goodsImg.deleteGoods.visibility = 'hidden';

        }
      },

    },
    data() {
      return {
        style: {
          container: {
            width: this.scale(1220) + 'px',
            marginLeft: this.scale(67) + 'px',
            marginRight: this.scale(67) + 'px',
            background: 'linear-gradient(to bottom, #eef0f1, #e2e3e4)',
          },
          centerContainer: {
            display: 'flex',
            justifyContent: 'center',
          },
          table: {
            width: this.scale(1130) + 'px',//一行中格子的总宽度。不能少，否则不会居中
            display: 'flex',
            flexWrap: 'wrap'
          },
        },// style end
        isCollectGridHover: false,
        collectTable: [
          {
            data: {
              href: '#',
              leftTopLabel: '热销',
              //imgSrc: require("../../assets/home/table/goods1.jpg"),
              imgSrc: "https://haitao.nos.netease.com/794f0d5a23ce49d09ec39fa632c547641524795679005jghbz7ju10199.jpg",
              title: '【自营包税】JMsolution 急救补水针剂面膜 35毫升/片 10片装',
              middleLabel: '干燥肌肤急救好物',
              discount: 69,
              originalPrice: 196,
            }
          },
          {
            data: {
              href: '#',
              leftTopLabel: '',
              imgSrc: "https://haitao.nos.netease.com/6509a31bc8714af4adf480822477ffd41521712597763jf2ce31m13432.jpg",
              title: '【新人专享】【网易自营 品牌授权】妆蕾 RAY 蚕丝面膜 金色10片+银色10片 共20片.net防伪查验',
              middleLabel: '唤醒年轻新肌肤',
              discount: 98,
              originalPrice: 308,
            }
          },
          {
            data: {
              href: '#',
              leftTopLabel: '热销',
              imgSrc: "https://haitao.nosdn2.127.net/imq35qx294_800_800.jpg",
              title: 'Freeplus 芙丽芳丝 净润洗面霜 100克 人见人爱',
              middleLabel: '人见人爱，超高人气',
              discount: 89,
              originalPrice: 150,
            }
          },
          {
            data: {
              href: '#',
              leftTopLabel: '热销',
              imgSrc: "https://haitao.nosdn2.127.net/8f4dff27105342febc4e31b4905c5a821543977568198jpakdo6p10110.jpg",
              title: 'herbacin 贺本清 小甘菊经典手霜 75毫升/支 2支装',
              middleLabel: '经典秋冬滋润选择',
              discount: 55,
              originalPrice: 158,
            }
          },
          {
            data: {
              href: '#',
              leftTopLabel: '热销',
              imgSrc: "https://haitao.nosdn1.127.net/4a5ff5ed7f4a41e598bb09613064995a1542768525344joqkjokt10051.jpg",
              title: 'LΛNEIGE 兰芝 雪纱丝柔防晒新款隔离霜 30毫升',
              middleLabel: '一抹防晒修颜',
              discount: 158,
              originalPrice: 249,
            }
          },
          {
            data: {
              href: '#',
              leftTopLabel: '热销',
              imgSrc: "https://haitao.nosdn2.127.net/1fdc7011647a44d18f1118f5ae9098861537544701032jmc6f04610339.jpg",
              title: 'AGE20`s 爱纪二十之 爱敬 精华遮瑕粉底 正装12.5克+替换装12.5克',
              middleLabel: '保湿MAX 秋冬裸妆必备神器',
              discount: 148,
              originalPrice: 288,
            }
          },
          {
            data: {
              href: '#',
              leftTopLabel: '',
              imgSrc: "https://haitao.nosdn1.127.net/a70fe1ee61f54991bbbb4e6f82dafd3e1539830438705jndzaal911832.jpg",
              title: '【干皮亲妈法国大宝】Embryolisse 保湿妆前隔离霜',
              middleLabel: '彩妆大师的秘密“武器”',
              discount: 68,
              originalPrice: 145,
            }
          }
        ]
      }
    },
    beforeMount() {
      // 收藏的商品
      this.collectTable.forEach((value, index, array) => {
        this.$set(array[index], 'style', {//用来设置每一个商品格子的样式。
          goods: {
            grid: {
              width: this.scale(200) + 'px',
              padding: this.scale(10) + 'px',// 用来显示格子hover时的边框
              position: 'relative',
            },
            unHoverBox: {// 未hover时的格子的实际显示区域
              background: 'white',
              padding: this.scale(4) + 'px',
              border: this.scale(2) + 'px solid #fff',
              visibility: 'visible',
            },
            hoverBox: {// hover时的格子的实际显示区域
              background: 'white',
              padding: this.scale(4) + 'px',
              visibility: 'hidden',
              border: this.scale(2) + 'px solid #ff4d51',
              position: 'absolute',
              zIndex: 1
            },
            goodsImg: {
              leftTopLabel: {
                position: 'absolute',
                width: this.scale(32) + 'px',
                height: this.scale(20) + 'px',
                lineHeight: this.scale(20) + 'px',
                backgroundColor: '#ff8385',
                color: '#fff',
                fontSize: this.scale(12) + 'px',
                textAlign: 'center',
              },
              deleteGoods: {
                position: 'absolute',
                float: 'right',
                visibility: 'hidden',
                left: this.scale(150) + 'px',
                top: this.scale(15) + 'px',
                width: this.scale(30) + 'px',
                height: this.scale(30) + 'px',
                lineHeight: this.scale(20) + 'px',
              },
              img: {
                display: 'block',
                width: this.scale(192) + 'px',
                height: this.scale(192) + 'px',
              }
            },
            bottomOfGoodsImg: {
              unHoverGoodsTitle: {
                fontSize: this.scale(14) + 'px',
                fontWeight: this.scale(700),
                whiteSpace: 'nowrap',
                display: 'inline-block',
                width: this.scale(192) + 'px',
                textOverflow: 'ellipsis',
                overflow: 'hidden',//只显示一行，使文字不溢出
              },
              hoverGoodsTitle: {
                fontSize: this.scale(14) + 'px',
                fontWeight: this.scale(700),
              },
              middleLabel: {
                marginTop: this.scale(6) + 'px',
                color: '#666',
                fontSize: this.scale(12) + 'px',
              },
              bottom: {
                box: {
                  marginTop: this.scale(5) + 'px',
                },
                moneySymbol: {
                  fontSize: this.scale(12) + 'px',
                  color: 'rgb(227, 20, 54)',
                },
                discount: {
                  fontSize: this.scale(20) + 'px',
                  fontWeight: this.scale(700),
                  color: 'rgb(227, 20, 54)',
                },
                originalPrice: {
                  color: '#999',
                  whiteSpace: 'normal',
                  lineHeight: this.scale(22) + 'px',
                  fontSize: this.scale(12) + 'px',
                },
                buy: {
                  display: 'inline-block',
                  height: this.scale(22) + 'px',
                  padding: '0 ' + this.scale(10) + 'px',
                  borderRadius: this.scale(3) + 'px',
                  background: '#e31436',
                  color: '#fff',
                  lineHeight: this.scale(22) + 'px',
                  textDecoration: 'none',
                  fontSize: this.scale(12) + 'px',
                  float: 'right',
                  cursor: 'pointer',
                }
              }
            }
          }
        });
      })
    },
    /* mounted() {
       this.$store.dispatch('user/updateBodyHeight', document.documentElement.scrollHeight + 'px')
     }*/
  }
</script>

<style scoped>
  .goods-title a {
    text-decoration: none;
    color: #000;
  }

  .goods-title a:hover {
    text-decoration: underline;
  }

  .buy:hover {
    opacity: .8;
  }
</style>




