<template>
  <div>
    <hx-home-header></hx-home-header>
    <!--商品选项-->
    <hx-goods-option :goods-parent-id="goodsParentId"
                     :evaluate-avg-score="evaluateAvgScore"
                     :have-img-count="haveImgCount"
                     :evaluate-count="evaluateCount">
    </hx-goods-option>
    <!--商品介绍和商品评价-->
    <div :style="style.tab.box">
      <hx-tab :tab-names="tabNames"
              :content-min-height="tabContentMinHeight"
              :tab-width="tabWidth"
              :fixed-tab-title="true"
              @tab-click="tabClick"
              :style="style.tab.tab">
        <template v-if="activeTabIndex == 0">
          <!--商品介绍-->
          <hx-goods-introduce :tab-width="tabWidth" :goods-parent-id="goodsParentId"></hx-goods-introduce>
        </template>
        <template v-else-if="activeTabIndex == 1">
          <!--商品评价-->
          <hx-goods-evaluate :goods-parent-id="goodsParentId"
                             :evaluate-count="evaluateCount"
                             :have-img-count="haveImgCount"
                             :add-evaluate-count="addEvaluateCount"
                             :evaluate-avg-score="evaluateAvgScore">
          </hx-goods-evaluate>
        </template>
      </hx-tab>
    </div>
  </div>
</template>

<script>

  import homeHeader from '../home/homeHeader'
  import goodsOption from '../goods/goodsOption'
  import tab from '../goods/tab'
  import goodsIntroduce from '../goods/goodsIntroduce'
  import goodsEvaluate from '../goods/goodsEvaluate'

  export default {
    name: "goodsDetail",
    components: {
      'hx-home-header': homeHeader,
      'hx-goods-option': goodsOption,
      'hx-tab': tab,
      'hx-goods-introduce': goodsIntroduce,
      'hx-goods-evaluate': goodsEvaluate,
    },

    props: {
      // 跳转到该页面的路由时父商品的id值
      goodsParentId: {}
    },

    data() {
      return {
        style: {
          tab: {
            box: {
              marginTop: this.scale(60) + 'px',
              marginBottom: this.scale(80) + 'px',
            },
            tab: {
              margin: '0 auto',
            }
          },
        },
        tabNames: ['商品介绍', '用户评价'],
        activeTabIndex: 0,
        tabWidth: 1000,
        tabContentMinHeight: 100,// 这个值不要改动
        evaluateCount: 0,
        haveImgCount: 0,
        addEvaluateCount: 0,
        evaluateAvgScore: 0,
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      tabClick(index) {
        this.activeTabIndex = index;
      },

      getGoodsEvaluateInfo() {
        this.$page.get("/goods/getGoodsEvaluateInfo", {goodsParentId: this.goodsParentId}, null,
          response => {
            this.evaluateCount = response.data.evaluateCount;
            this.haveImgCount = response.data.haveImgCount;
            this.addEvaluateCount = response.data.addEvaluateCount;
            this.evaluateAvgScore = parseFloat(response.data.evaluateAvgScore.toFixed(1));
            this.tabNames[1] = '用户评价 ' + this.evaluateCount
          })
      }
    },

    mounted() {
      this.getGoodsEvaluateInfo();
    },


  }
</script>

<style scoped>

</style>



