<template>
  <div>
    <div :style="style.backgroundBox">
      <!-- header -->
      <div :style="style.header.box">
        <div :style="style.header.home" @click="homeClick">
          王姐超市欢迎您!
        </div>
      </div>
      <!--表单-->
      <div :style="style.centerContainer">
        <el-form :style="style.forgetPasswordForm.box"
                 ref="forgetPasswordForm"
                 status-icon
                 :model="forgetPasswordForm"
                 :rules="rules">
          <div :style="style.forgetPasswordForm.titleBox">
            找回密码
          </div>
          <!--验证码-->
          <div :style="style.forgetPasswordForm.itemBox">
            <span :style="style.forgetPasswordForm.labelText">验证码:</span>
            <span :style="style.forgetPasswordForm.captcha.input">
              <el-form-item prop="captcha">
                 <el-input type="text" :disabled="disabledForm.captchaDisabled" v-model="forgetPasswordForm.captcha"
                           autocomplete="off" maxLength=4
                           placeholder="请输入验证码">
                   <i slot="prefix" class="captcha"></i>
                 </el-input>
              </el-form-item>
            </span>
            <img @click="getCaptcha"
                 :style="style.forgetPasswordForm.captcha.img"
                 :src="captchaImgSrc" alt="点击重试"/>
          </div>
          <!--邮箱-->
          <div v-if="EmailInputBoxVisible" :style="style.forgetPasswordForm.itemBox">
            <span :style="style.forgetPasswordForm.labelText">邮箱:</span>
            <span :style="style.forgetPasswordForm.email">
              <el-form-item prop="email">
                <el-input type="text" :disabled="disabledForm.emailDisabled" v-model="forgetPasswordForm.email"
                          autocomplete="on" placeholder="请输入您的邮箱">
                  <i slot="prefix" class="email"></i>
                </el-input>
              </el-form-item>
            </span>
          </div>
          <!--认证码-->
          <div v-if="authenticationInputBoxVisible" :style="style.forgetPasswordForm.itemBox">
            <span :style="style.forgetPasswordForm.labelText">认证码:</span>
            <span :style="style.forgetPasswordForm.authenticationInputBox">
              <el-form-item prop="authenticationCode">
                <el-input type="text" :disabled="disabledForm.authenticationCodeDisabled"
                          v-model="forgetPasswordForm.authenticationCode" autocomplete="off" maxLength=6
                          placeholder="请输入认证码">
                  <i slot="prefix" class="auth"></i>
                </el-input>
              </el-form-item>
            </span>
          </div>
          <div v-if="passwordInputBoxVisible">
            <!--密码-->
            <div :style="style.forgetPasswordForm.itemBox">
              <span :style="style.forgetPasswordForm.labelText">密码:</span>
              <span :style="style.forgetPasswordForm.password">
                <el-form-item prop="password">
                  <el-input type="password"
                            v-model="forgetPasswordForm.password" autocomplete="off" placeholder="请输入密码">
                    <i slot="prefix" class="password"></i>
                  </el-input>
                </el-form-item>
            </span>
            </div>
            <!--确认密码-->
            <div v-if="passwordInputBoxVisible" :style="style.forgetPasswordForm.itemBox">
              <span :style="style.forgetPasswordForm.labelText">确认密码:</span>
              <span :style="style.forgetPasswordForm.password">
                <el-form-item prop="confirmPassword">
                  <el-input type="password"
                            v-model="forgetPasswordForm.confirmPassword" autocomplete="off"
                            placeholder="请再输入一次密码">
                    <i slot="prefix" class="password"></i>
                  </el-input>
                </el-form-item>
            </span>
            </div>
            <!--提交按钮-->
            <div :style="style.forgetPasswordForm.itemBox">
              <div :style="style.forgetPasswordForm.submitBtn">
                <el-button @click="onSubmit">修改</el-button>
              </div>
            </div>
          </div>
        </el-form>
      </div>
    </div>
  </div>

</template>

<script>

  export default {
    components: {},
    data() {
      let _this = this;
      var validateCaptcha = (rule, value, callback) => {
        _this.$page.post(_this.baseContextPath + '/checkCaptcha', null, {'captcha': _this.forgetPasswordForm.captcha},
          response => {
            if (response.data !== true) {
              callback(new Error('验证码错误'));
            }
            else {
              _this.isCaptchaValidate = true;//验证成功后无法点击验证码
              _this.disabledForm.captchaDisabled = true;
              _this.style.cursor = null;
              //_this.style.forgetPasswordForm.height = _this.scale(140) + 'px';
              _this.EmailInputBoxVisible = true;
              callback();
            }
          });
      };
      var validateEmail = (rule, value, callback) => {
        _this.$page.post(_this.baseContextPath + '/checkEmail', null, {'email': _this.forgetPasswordForm.email},
          response => {
            if (response.data !== true) {
              callback(new Error('此邮箱未注册,请重新输入'));
            } else {
              _this.disabledForm.emailDisabled = true;
              _this.$alert('包含认证码的邮件已发送到您的邮箱,请查收!', {
                confirmButtonText: '确定',
              });
              //_this.style.forgetPasswordForm.height = _this.scale(200) + 'px';
              _this.authenticationInputBoxVisible = true;
              callback();
            }
          },
          error => {
            callback(new Error('服务器繁忙,请稍后再试!'));
            setTimeout(1500);
          });
      };
      var validateAuthenticationCode = (rule, value, callback) => {
        _this.$page.post(_this.baseContextPath + '/checkAuthenticationCode', null, {'authenticationCode': _this.forgetPasswordForm.authenticationCode},
          response => {
            if (response.data !== true) {
              callback(new Error('认证码错误'));
            } else {
              _this.disabledForm.authenticationCodeDisabled = true;
              //_this.style.forgetPasswordForm.height = _this.scale(380) + 'px';
              _this.passwordInputBoxVisible = true;
              callback();
            }
          })
      };
      var validatePassword = (rule, value, callback) => {
        if (/^[0-9]*$/.test(value)) {
          callback(new Error('密码不能全为数字'))
        } else if (/^[a-zA-Z]*$/.test(value)) {
          callback(new Error('密码不能全为字母'))
        } else {
          if (_this.forgetPasswordForm.confirmPassword !== '') {//判断第二个密码框是否没有输入
            _this.$refs.forgetPasswordForm.validateField('confirmPassword');
          }
          callback();
        }

      };
      var validateConfirmPassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== _this.forgetPasswordForm.password) {//如果与第一个密码框的值不相等
          callback(new Error('两次输入密码不一致!'));//提示信息: 两次输入密码不一致
        } else {
          callback();
        }
      };

      return {
        style: {
          header: {
            box: {
              backgroundColor: '#57555e',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
            },
            home: {
              marginLeft: this.scale(50) + 'px',
              float: 'left',
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer'
            },
          },
          backgroundBox: {// 用来设置背景图
            backgroundImage: "url(" + require("../../assets/user/bg.jpg") + ")",
            backgroundSize: this.$store.state.app.visibleWidth + 'px ' + (this.$store.state.app.visibleWidth) / 1920 * 1152 + 'px',
            backgroundRepeat: 'no-repeat',
            height: (this.$store.state.app.visibleWidth) / 1920 * 1152 + 'px',
            width: this.$store.state.app.visibleWidth + 'px',
          },
          centerContainer: {
            paddingTop: this.scale(50) + 'px',
            margin: '0 auto',
            width: this.scale(460) + 'px',
          },
          forgetPasswordForm: {
            box: {
              margin: '0 auto',
              padding: this.scale(25) + "px 0",
              //height: this.scale(80) + 'px',
              // height: this.scale(420) + 'px',
              background: 'linear-gradient(to bottom, #CDC0B0, #EED8AE, #C9C9C9)',
              border: this.scale(0) + 'px solid #00CED1',
              borderRadius: this.scale(20) + 'px',
            },
            titleBox: {
              width: this.scale(120) + 'px',
              margin: '0 auto',
              color: '#49850a',
              fontWeight: 'bold',
            },
            itemBox: {
              margin: this.scale(20) + "px " + '0px',
            },
            labelText: {
              color: '#777a7e',
              verticalAlign: "middle",
              marginLeft: this.scale(40) + 'px',
              display: 'inline-block',
              width: this.scale(100) + 'px',
              verticalAlign: "middle",
            },
            captcha: {
              input: {
                display: 'inline-block',
                width: this.scale(150) + 'px',
                verticalAlign: "middle",
              },
              img: {
                display: 'inline-block',
                fontSize: this.scale(10) + 'px',
                verticalAlign: "middle",
                cursor: 'pointer',
                width: this.scale(100) + 'px',
                marginLeft: this.scale(10) + 'px'
              }
            },
            email: {
              display: 'inline-block',
              width: this.scale(230) + 'px',
              verticalAlign: "middle",
            },
            authenticationInputBox: {
              display: 'inline-block',
              width: this.scale(150) + 'px',
              verticalAlign: "middle",
            },
            password: {
              display: 'inline-block',
              width: this.scale(250) + 'px',
              verticalAlign: "middle",
            },
            submitBtn: {
              margin: '0 auto',
              width: this.scale(70) + 'px',
            }
          },

        },
        forgetPasswordForm: {
          captcha: null,
          email: null,
          authenticationCode: null,
          password: null,
          confirmPassword: null,
        },
        rules: {
          captcha: [
            {required: true, message: '请输入验证码', trigger: ['blur']},
            {validator: validateCaptcha, trigger: ['blur']}
          ],
          email: [{type: "email", message: '请输入合法的邮箱地址', required: true, trigger: ['blur']},
            {validator: validateEmail, trigger: ['blur']}],
          authenticationCode: [
            {required: true, message: '请输入认证码', trigger: ['blur']},
            {validator: validateAuthenticationCode, trigger: ['blur']}
          ],
          password: [{required: true, message: '密码不能为空', trigger: ['blur']},
            {min: 6, message: '密码长度不能小于6位', trigger: ['blur']},
            {validator: validatePassword, trigger: ['change', 'blur']}],
          confirmPassword: [{required: true, message: '密码不能为空', trigger: ['blur']},
            {min: 6, message: '密码长度不能小于6位', trigger: ['blur']},
            {validator: validateConfirmPassword, trigger: ['change', 'blur']}],
        },
        baseContextPath: this.$route.path,
        captchaImgSrc: null,//nginx中的图片路径
        isCaptchaValidate: false,//验证成功后就不发送验证码请求
        EmailInputBoxVisible: false,
        authenticationInputBoxVisible: false,
        passwordInputBoxVisible: false,
        disabledForm: {//禁止如下的输入控件验证成功后再次输入
          captchaDisabled: false,
          emailDisabled: false,
          authenticationCodeDisabled: false,
          /* passwordDisabled: false,
           confirmPasswordDisabled: false,*/
        },
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      homeClick() {
        this.$router.push({name: "home"})
      },
      onSubmit() {
        let _this = this;//必须加let,否则会报错: vueThis未定义
        let arr = ['password', 'confirmPassword'];//对部分字段进行校验
        let validate = true;//假设数据合法
        _this.$refs.forgetPasswordForm.validateField(arr, (errorMessage, invalidField) => {
          if (invalidField != null) {
            validate = false;
          }
        });
        if (validate) {
          _this.$page.post(
            _this.baseContextPath + '/resetPassword',
            null,
            _this.forgetPasswordForm,
            response => {
              _this.$alert('重置密码成功!', {
                confirmButtonText: '确定',
                callback: (action, instance) => {
                  setTimeout(function () {
                    _this.$router.push({name: "login", params: {tabName: 'login'}})
                  }, 200)
                }
              });
            },
            response => {
              _this.$message({
                message: '很抱歉,更新密码失败,请稍后重试!',
                type: 'error',
                center: true,
              });
              setTimeout(2000)
            });
        }
      },
      getCaptcha() {//验证后就无法再获取验证码
        let _this = this;
        if (!this.isCaptchaValidate) {
          this.$page.get(_this.baseContextPath + '/getCaptcha', null, null,
            response => {
              _this.forgetPasswordForm.captcha = null
              _this.captchaImgSrc = '/temp/images/captcha/' + response.data;
            }
          );
        }
      }
      ,
    }
    ,
    mounted() {
      /*this.style.forgetPasswordContainer.height = window.screen.height * window.devicePixelRatio + 'px';
      this.style.forgetPasswordContainer.width = window.screen.width * window.devicePixelRatio  - window.innerWidth + document.body.clientWidth+ 'px';
     */
      this.getCaptcha();
    }
  }
</script>

<style scoped>

  .el-form-item {
    margin-bottom: 0px;
  }

  .captcha {
    background: url(../../assets/user/captcha-icon.png) center no-repeat;
    background-size: cover;
  }

  .captcha:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .email {
    background: url(../../assets/user/email-icon.png) center no-repeat;
    background-size: cover;
  }

  .email:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .auth {
    background: url(../../assets/user/auth-icon.png) center no-repeat;
    background-size: cover;
  }

  .auth:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .password {
    background: url(../../assets/user/password-icon.png) center no-repeat;
    background-size: cover;
  }

  .password:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

</style>



