//一个模块(由多个组件构成,这些组件由要用到共享数据)定义一个vuex js文件,而不是一个组件定义一个vuex js文件.
//如果一个模块只有一个组件,那就没必要定义vuex js文件

function scale(value) {
  //必须使用parseInt进行取整，因为el的span,push不能处理小数
  return parseInt(value * window.screen.width * window.devicePixelRatio / 1366.0)
}

const state = {
  user: {
    id: null,
    username: null,
    userInfo: {
      nickname: null,
      sex: null,
      birthday: 0,
      hometown: "['510000', '510100', '510116']",
      deliverAddress: "['510000', '510100', '510116']",// 收货地址
      addressDetail: null,// 收货详细地址
      avatarSrc: '',
    }
  }, // 保存登录后的用户信息
}

const getters = {
  /*height() {
    return state.userInfo.style.container.height;
  }*/
}

const mutations = {
  updateUser(state, value) {
    state.user = value;
  },
}

const actions = {
  updateUser({commit}, value) {
    commit('updateUser', value);
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



