<template>
  <div :style="style.container">
    <dl id="tab" :style="style.tab.box">
      <!--tab头-->
      <dt>
        <ul id="tab_ul" :style="style.tab.ul">
          <li v-for="tabName in tabNames"
              :style="style.tab.li"
              :id="'tab_li'+tabNames.indexOf(tabName)"
              @click="tabClick($event,'tab_li','tab_li_text',tabNames.indexOf(tabName))">
            <span :id="'tab_li_text'+tabNames.indexOf(tabName)">{{tabName}}</span>
          </li>
        </ul>
      </dt>
      <!--tab内容-->
      <dd :style="style.tab.content">
        <slot></slot>
      </dd>
    </dl>
  </div>
</template>

<script>
  export default {
    name: "tab",
    props: {
      // 所有标签的名字
      tabNames: {
        type: Array,
        required: true,
      },
      // 整个标签页的宽度，默认300px
      tabWidth: {
        type: Number,
        default: 300,
      },
      //标签页内容的高度，默认200px
      contentMinHeight: {
        type: Number,
        default: 200,
      },
      // 默认激活的标签页索引
      activeTabIndex: {
        type: Number,
        default: 0,
      },
      // 当tab被滚动到顶部时固定tab头
      fixedTabTitle: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        style: {
          container: {
            width: this.tabWidth + 'px',
          },
          tab: {
            box: {
              border: this.scale(1) + 'px solid #ccc',
            },
            ul: {
              fontSize: '0px',
              position: 'static',
              top: null,
              zIndex: null,
              backgroundColor: null,
            },
            li: {
              display: 'inline-block',
              cursor: 'pointer',
              fontSize: this.scale(16) + 'px',
              borderLeft: this.scale(1) + 'px solid #ccc',
              borderBottom: this.scale(1) + 'px solid #fff',
              width: null,
              textAlign: 'center',
              borderTop: this.scale(3) + 'px solid #fff',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
            },
            content: {
              minHeight: this.contentMinHeight + 'px',
              marginTop: null,
            }
          }
        },// style end
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      deleteTabStyle(tabLiId, tabLiTextId) {
        this.tabNames.forEach((value, index) => {
          // tabLi
          let tabLi = document.querySelector("#" + tabLiId + index);
          tabLi.style.borderTop = '3px solid #fff'
          tabLi.style.borderBottom = '1px solid #ccc'
          // tabLiText
          let tabLiText = document.querySelector("#" + tabLiTextId + index);
          tabLiText.style.color = 'black'
          tabLiText.style.fontWeight = 'normal'
        })
      },
      addTabStyle(tabLiId, tabLiTextId, index) {
        // tabLi
        let tabLi = document.querySelector("#" + tabLiId + index);
        tabLi.style.borderTop = '3px solid #d41c44'
        tabLi.style.borderBottom = '1px solid #fff'
        // tabLiText
        let tabLiText = document.querySelector("#" + tabLiTextId + index);
        tabLiText.style.color = '#d41c44'
        tabLiText.style.fontWeight = 'bold'
      },
      /**
       * 处理tab被点击
       * @event
       * @tabLiId
       * @tabLiTextId
       * @index 被点击的tab的索引
       */
      tabClick(event, tabLiId, tabLiTextId, index) {
        this.deleteTabStyle(tabLiId, tabLiTextId);
        this.addTabStyle(tabLiId, tabLiTextId, index)
        this.$emit('tab-click', index) // 把事件(tab-click)和值(index)抛出去
        let tab = document.querySelector("#tab");
        window.scrollTo(0, tab.offsetTop)
      },
      initTab() {
        let tab_ul = document.querySelector("#tab_ul");
        let tab_ul_children = tab_ul.children;
        for (let i = 0; i < tab_ul_children.length; i++) {// 不要使用forEach()遍历,有兼容问题,其他地方的数组一般可以使用forEach().
          if (i == 0) {
            tab_ul_children[i].style.borderLeft = '0px solid #ccc'
          }
        }
        let liCount = tab_ul_children.length;
        this.style.tab.li.width = (this.tabWidth - 2 - (liCount - 1) * 1) / liCount + 'px';// -2 是减去两边的边框宽度
        this.deleteTabStyle('tab_li', 'tab_li_text');
        this.addTabStyle('tab_li', 'tab_li_text', this.activeTabIndex)// 默认打开第一个标签
      },
      /**
       * 处理tab内容滚动
       */
      handleTabScroll() {
        let ticking = false;// 防止高频率触发onScroll()
        window.onscroll = () => {
          if (!ticking) {
            window.requestAnimationFrame(() => {
              this.doFixedTabTitle();
              ticking = false;
            });
          }
          ticking = true;
        }
      },
      doFixedTabTitle() {
        let tab = document.querySelector("#tab");
        let tab_ul = document.querySelector("#tab_ul");
        if (tab.getBoundingClientRect().top <= 0) {
          this.style.tab.ul.position = 'fixed';
          this.style.tab.ul.top = '0px';
          this.style.tab.ul.zIndex = 999;
          this.style.tab.ul.backgroundColor = 'white';
          this.style.tab.content.marginTop = tab_ul.offsetHeight + 'px';// 也就是tab头的高度
        } else {
          this.style.tab.ul.position = 'static'
          this.style.tab.ul.top = null;
          this.style.tab.ul.zIndex = null;
          this.style.tab.ul.backgroundColor = null;
          this.style.tab.content.marginTop = null;
        }
      }
    },
    mounted() {
      this.initTab();
      if (this.fixedTabTitle) {
        this.handleTabScroll();
      }
    },
    beforeDestroy() {
      window.onscroll = null;// 解绑，否则doFixedTabTitle()会报错
    }
  }
</script>

<style scoped>

  dl, dt, dd, ul, ol, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

</style>



