<template>
  <div>
    <div :style="style.backgroundBox">
      <!-- header -->
      <div :style="style.header.box">
        <div :style="style.header.home" @click="homeClick">
          王姐超市欢迎您!
        </div>
      </div>
      <div ref="login" :style="style.loginContainer">
        <!--tab，用来显示登录和注册-->
        <el-tabs id="tabs" :style="style.tabs" type="border-card" stretch v-model="activeName"
                 @tab-click="handleTabClick">
          <el-tab-pane label="登录" name="login">
            <!--定义登录面板的显示内容-->
            <el-form ref="loginForm"
                     :model="loginForm"
                     :rules="rules">
              <!--用户名-->
              <div :style="style.loginForm.itemBox">
                <div :style="style.loginForm.username">
                  <el-form-item prop="username">
                    <el-input type="text" v-model="loginForm.username" autocomplete="on" placeholder="请输入用户名或邮箱">
                      <i slot="prefix" class="username"></i>
                    </el-input>
                  </el-form-item>
                </div>
              </div>
              <!--密码-->
              <div :style="style.loginForm.itemBox">
                <div :style="style.loginForm.password">
                  <el-form-item prop="password">
                    <el-input type="password" v-model="loginForm.password" autocomplete="off"
                              placeholder="请输入密码">
                      <i slot="prefix" class="password"></i>
                    </el-input>
                  </el-form-item>
                </div>
              </div>
              <!--验证码-->
              <div :style="style.loginForm.itemBox">
                <div :style="style.loginForm.captcha.input">
                  <el-form-item prop="captcha">
                    <el-input type="text" v-model="loginForm.captcha" autocomplete="off" maxLength=4
                              placeholder="请输入验证码">
                      <i slot="prefix" class="captcha"></i>
                    </el-input>
                  </el-form-item>
                </div>
                <img @click="getCaptcha"
                     :style="style.loginForm.captcha.img"
                     :src="captchaImgSrc" alt="点击重试"/>
              </div>
              <!--7天免登录-->
              <div :style="style.loginForm.btn.box">
                <span :style="style.loginForm.btn.switch">
                  <el-switch
                    v-model="loginForm.rememberMe"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    active-text="7天免登录">
                  </el-switch>
                </span>
                <span :style="style.loginForm.btn.loginBtn">
                  <el-button type="success" size="small" @click="onSubmit">登录</el-button>
                </span>
                <span :style="style.loginForm.btn.forgetPasswordBtn">
                  <el-button type="info" size="small" @click="forgetPassword()">忘记密码</el-button>
                </span>
              </div>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="邮箱注册" name="registe">
            <!--定义邮箱面板的显示内容-->
            <registe ref="registe"></registe>
          </el-tab-pane>
        </el-tabs>
        <!--&lt;!&ndash;用来显示忘记密码&ndash;&gt;
        <router-view name="forgetPassword"></router-view>-->
      </div>
    </div>
  </div>

</template>

<script>
  import registe from './registe'

  export default {
    props: {
      tabName: {
        default: 'login'
      }
    },
    components: {
      registe
    },
    data() {
      let _this = this;
      var validateCaptcha = (rule, value, callback) => {
        _this.$page.post(
          _this.baseContextPath + '/checkCaptcha',
          null,
          {captcha: _this.loginForm.captcha},
          response => {
            if (response.data == false) {
              callback(new Error('验证码错误'));
            }
            else {
              _this.isCaptchaValidate = true;//验证成功后无法点击验证码
              _this.style.cursor = null;
              callback();
            }
          })
      };
      return {
        style: {
          backgroundBox: {// 用来设置背景图
            backgroundImage: "url(" + require("../../assets/user/bg.jpg") + ")",
            backgroundSize: this.$store.state.app.visibleWidth + 'px ' + (this.$store.state.app.visibleWidth) / 1920 * 1152 + 'px',
            backgroundRepeat: 'no-repeat',
            height: (this.$store.state.app.visibleWidth) / 1920 * 1152 + 'px',
            width: this.$store.state.app.visibleWidth + 'px',
          },
          header: {
            box: {
              backgroundColor: '#57555e',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
            },
            home: {
              marginLeft: this.scale(50) + 'px',
              float: 'left',
              fontWeight: 'bold',
              color: '#c4bade',
              fontSize: this.scale(16) + 'px',
              cursor: 'pointer'
            },
          },
          loginContainer: {
            width: this.scale(500) + 'px',
            margin: '0 auto',
          },
          tabs: {//初始的tab样式
            padding: this.scale(10) + "px",
            marginTop: this.scale(50) + 'px',
            background: 'linear-gradient(to bottom, #CDC0B0, #EED8AE, #C9C9C9)',
            border: this.scale(0) + 'px solid #00CED1',
            borderRadius: this.scale(20) + 'px',
          },
          loginForm: {
            itemBox: {
              width: this.scale(350) + 'px',
              margin: this.scale(20) + "px " + 'auto',
            },
            username: {
              verticalAlign: "middle",
            },
            password: {
              verticalAlign: "middle",
            },
            captcha: {
              input: {
                display: 'inline-block',
                width: this.scale(200) + 'px',
                verticalAlign: "middle",
              },
              img: {
                display: 'inline-block',
                fontSize: this.scale(10) + 'px',
                verticalAlign: "middle",
                cursor: 'pointer',
                width: this.scale(100) + 'px',
                marginLeft: this.scale(10) + 'px'
              },
            },
            btn: {
              box: {
                width: this.scale(350) + 'px',
                margin: '0 auto',
              },
              switch: {
                marginRight: this.scale(10) + 'px',
                display: 'inline-block',

              },
              loginBtn: {
                marginRight: this.scale(10) + 'px'
              },
              forgetPasswordBtn: {
                float: 'right',
              }
            }
          }

        },
        loginForm: {
          username: null,
          password: null,
          rememberMe: true,
          captcha: null
        },
        rules: {
          username: [{required: true, message: '用户名不能为空', trigger: ['blur']}],
          password: [{required: true, message: '密码不能或空', trigger: ['blur']}],
          captcha: [
            {required: true, message: '请输入验证码', trigger: ['blur']},
            {validator: validateCaptcha, trigger: ['blur']}
          ],
        },
        baseContextPath: this.$route.path,
        activeName: this.tabName,
        captchaImgSrc: null,//值为nginx中的图片路径
        isCaptchaValidate: false,//当后端验证输入的验证码正确后，使它变为true
      }
    },
    //data end
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      homeClick() {
        this.$router.push({name: "home"})
      },
      onSubmit() {
        let _this = this;//必须加let,否则会报错: _this未定义
        _this.$refs.loginForm.validate((valid) => {//提交时再验证一下表单的数据(防止什么都不输入直接提交).
          if (valid) {//如果校验结果合法
            _this.$page.post(_this.baseContextPath + '/checkIdentify',
              null,
              _this.loginForm,
              response => {
                if (response.data == true) {//登录成功
                  _this.$message({
                    message: "登录成功",
                    type: 'success',
                    center: true,
                  });
                  setTimeout(function () {
                    if (_this.$root.beforeAuthorizationPath != null) {
                      _this.$router.push(_this.$root.beforeAuthorizationPath);//重定向到认证前的路径
                    } else {
                      _this.$router.push({name: 'home'});//重定向到主页
                    }
                  }, 100)
                } else {
                  _this.$message({
                    message: "用户名或密码错误",
                    type: 'error',
                    center: true,
                  });
                }
              },
            )
          } else {
            return false;//一定要返回false代表提交时未验证
          }
        });
      },
      forgetPassword() {
        this.$router.push({name: "forgetPassword"});
      },
      handleTabClick(tab, event) {
        if (tab.name == 'login') {
          this.getCaptcha();
          //this.style.tabs.height = this.scale(320) + 'px'
        } else if (tab.name == 'registe') {
          this.$refs.registe.getCaptcha();
          //this.style.tabs.height = this.scale(420) + 'px'
        }
      },
      getCaptcha() {
        let _this = this;
        if (!this.isCaptchaValidate) {//验证后就无法再获取验证码
          _this.$page.get(_this.baseContextPath + '/getCaptcha', null, null,
            response => {
              _this.loginForm.captcha = null
              _this.captchaImgSrc = '/temp/images/captcha/' + response.data;
            }
          );
        }
      },
    },

    beforeRouteEnter(to, from, next) {
      next(vm => {
        if (vm.$store.state.userInfo.user.username != null) {// 如果已经登录过,那么不允许再进入登录页面
          vm.$message({
            message: "已经登录过，请先注销！",
            type: 'warning',
            center: true,
          });
          vm.$router.push(from.path);// 返回进入该页面前的路由
        }
      })
    },

    //methods end
    mounted() {
      if (this.activeName == 'login') {
        this.getCaptcha();
        //this.style.tabs.height = this.scale(320) + 'px'
      } else if (this.activeName == 'registe') {
        this.$refs.registe.getCaptcha();
        //this.style.tabs.height = this.scale(420) + 'px'
      }
    },
    //mounted end
  }
</script>

<style scoped>

  .el-form-item {
    margin-bottom: 0px;
  }

  .username {
    background: url(../../assets/user/username-icon.png) center no-repeat;
    background-size: cover;
  }

  .username:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .captcha {
    background: url(../../assets/user/captcha-icon.png) center no-repeat;
    background-size: cover;
  }

  .captcha:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }

  .password {
    background: url(../../assets/user/password-icon.png) center no-repeat;
    background-size: cover;
  }

  .password:before {
    content: "替";
    font-size: 16px;
    visibility: hidden;
  }
</style>



